                srcset="../assets/done_24px.png 1x, ../assets/done_24px_2x.png 2x"
              />
              <p>Download in any browser</p>
            </li>
          </ul>
          <router-link to="/web/welcome" class="web button-2">CONTINUE TO WEB APP</router-link>
        </div>
        <!-- Desktop app item-->
        <div class="column desktopapp">
          <h5 class="h5">
            BlackHole for
            <span v-show="os === 'Mac OS'">macOS</span>
            <span v-show="os === 'Windows'">windows</span>
          </h5>
          <ul class="body-2 features">
            <li class="item">
              <img
                src="../assets/done_24px.png"
                alt="Done icon"
                srcset="../assets/done_24px.png 1x, ../assets/done_24px_2x.png 2x"
              />
              <p>
