<template>
  <div class="navbar-block" :class="[openMenu || scrolled ? 'changecolor' : '']">
    <div class="contents-limiter">
      <nav
        :class="[openMenu || scrolled ? 'changecolor' : '']"
        class="navbar"
        role="navigation"
        aria-label="main navigation"
      >
        <div class="navbar-brand">
          <router-link to="/" @click.native="navbarHomeClick" class="navbar-item">
            <img
              src="../assets/‌logo_24_wh.png"
              srcset="../assets/‌logo_24_wh.png 1x, ../assets/‌logo_24_wh_2x.png 2x"
              class="is-hidden-tablet"
              alt="BlackHole Logo"
            />
            <img
              src="../assets/‌logo_32_wh.png"
              srcset="../assets/‌logo_32_wh.png 1x, ../assets/‌logo_32_wh_2x.png 2x"
              class="is-hidden-mobile"
              alt="BlackHole Logo"
            />
          </router-link>

          <a
            @click="openMenu = !openMenu"
            :class="{ 'is-active': openMenu }"
            class="navbar-burger burger"
            ref="hambergerMenu"
            role="button"
            aria-label="menu"
            aria-expanded="false"
            v-show="visibleButton"
          >
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
          </a>
        </div>

        <div v-show="visibleButton" class="navbar-menu">
          <div class="navbar-start"></div>

          <div class="navbar-end">
            <div class="navbar-item">
              <!-- <a
                href="mailto:hi@blackhole.run"
                @click="navbarContactClick"
                class="button-2 link2 contact"
                targer="_blank"
              >contact</a>-->
              <router-link
                to="/select-os"
                @click.native="navbarDownloadClick"
                class="button-2 link2 download"
              >download</router-link>
              <router-link to="/login" class="login button-2" v-show="isVisible">log in</router-link>
              <button @click="signOutBtnClick" class="login button-2" v-show="!isVisible">log out</button>
              <router-link to="/web" class="signup button-2" v-show="!isVisible">go to app</router-link>
              <button @click="signUpBtnClick" class="signup button-2" v-show="isVisible">sign up</button>
            </div>
          </div>
        </div>
      </nav>

      <div class="dynamic-menu" :class="[{ sliddown: openMenu }, { changecolor: openMenu }]">
        <div class="contents-limiter">
          <!-- <a
            href="mailto:hi@blackhole.run"
            @click="navbarContactClick"
            class="button-2 link2 contact"
            targer="_blank"
          >contact</a>-->
          <router-link
            to="/select-os"
            @click="navbarDownloadClick"
            class="button-2 link2 download"
          >download</router-link>
          <router-link to="/login" class="login button-2" v-show="isVisible">{{ logText }}</router-link>
          <router-link to="/sign-up" class="signup button-2" v-show="isVisible">sign up</router-link>
          <button @click="signOutBtnClick" class="login button-2" v-show="!isVisible">log out</button>
          <router-link to="/web" class="signup button-2" v-show="!isVisible">go to app</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { userSession } from "../user";
export default {
  props: {
    visibleButton: Boolean
  },
  data() {
    return {
      openMenu: false,
      scrolled: false,
      logText: "log in",
      isVisible: true
    };
  },

  mounted() {
    this.isVisible = !userSession.isUserSignedIn();
  },
  methods: {
    documentClick(e) {
      let el = this.$refs.hambergerMenu;
      let target = e.target;
      if (el !== target && !el.contains(target)) {
        this.openMenu = false;
      }
    },

    handelScroll() {
      if (window.innerWidth < 768) {
        this.scrolled = window.scrollY > 64;
      } else {
        this.scrolled = window.scrollY > 80;
      }
    },

    navbarHomeClick() {
      this.$ga.event({
        eventCategory: "Router",
        eventAction: "from_navbar_to_home",
        eventLabel: "navbarHomeClick",
        eventValue: 0
      });
    },

    navbarContactClick() {
      const mobileView = window.matchMedia("(max-width: 767px)");
      if (mobileView.matches) {
        this.$ga.event({
          eventCategory: "Hyperlink",
          eventAction: "from_mobile_navbar_to_cantact",
          eventLabel: "navbarContactClick",
          eventValue: 0
        });
      } else {
        this.$ga.event({
          eventCategory: "Hyperlink",
          eventAction: "from_desktop_navbar_to_cantact",
          eventLabel: "navbarContactClick",
          eventValue: 0
        });
      }
    },

    navbarDownloadClick() {
      const mobileView = window.matchMedia("(max-width: 767px)");
      if (mobileView.matches) {
        this.$ga.event({
          eventCategory: "Router",
          eventAction: "from_mobile_navbar_to_select_os",
          eventLabel: "navbarDownloadClick",
          eventValue: 0
        });
      } else {
        this.$ga.event({
          eventCategory: "Router",
          eventAction: "from_desktop_navbar_to_select_os",
          eventLabel: "navbarDownloadClick",
          eventValue: 0
        });
      }
    },

    signUpBtnClick() {
      this.$router.push({ path: "sign-up" });
    },
    signOutBtnClick() {
      userSession.signUserOut();
      window.location.reload();
    }
  },

  created() {
    document.addEventListener("click", this.documentClick);
    document.addEventListener("scroll", this.handelScroll);
  },
  destroyed() {
    // important to clean up!!
    document.removeEventListener("click", this.documentClick);
    document.addEventListener("scroll", this.handelScroll);
  }
};
</script>

<style lang="scss" scoped>
.navbar-block {
  background-color: $bh;
  transition: background-color 200ms cubic-bezier(0.23, 1, 0.32, 1);

  @include until($tablet) {
    .contents-limiter {
      margin: 0;
    }
  }
}

.navbar {
  min-height: 80px;
  background-color: $bh;
  transition: background-color 200ms cubic-bezier(0.23, 1, 0.32, 1);

  @include until($tablet) {
    min-height: 64px;
  }
}

.navbar-item {
  display: flex;
  align-items: center;
  padding: 0;
  min-height: 64px;
  img {
    width: 164px;
    height: auto;
    min-height: 32px;

    @include until($tablet) {
      width: 132px;
      height: auto;
      min-height: 24px;
      margin: 0 0 0 24px;
    }
  }
}

.navbar-burger {
  width: 64px;
  height: 64px;
  padding: 0 4px 0 0;

  &:hover {
    background-color: transparent;
  }

  span {
    background-color: $wh;
    height: 2px;
    width: 18px;
  }
}

.link2 {
  color: $wh;
  padding: 4px 0;

  &:hover {
    color: $wh;
  }
}

.contact {
  margin: 0 20px 0 0;
}

.download {
  margin: 0 20px 0 0;
  transition: color 150ms ease-in-out;
  &:hover {
    color: $wm;
  }
}

.login {
  color: $wh;
  margin: 0 20px 0;
  transition: color 150ms ease-in-out;
  &:hover {
    color: $wm;
  }
}

.signup {
  color: $bh;
  padding: 11px 24px 9px;
  margin: 0 0 0 20px;
  background-color: $p1;
  transition: background-color 150ms ease-in-out;
  &:hover {
    background-color: $hover1;
  }

  &:active {
    background-color: $press1;
  }
}

.dynamic-menu {
  min-height: 64px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin: -64px -24px 0 -24px;
  background-color: $bh;
  transform: translate(0, 0);
  transition: transform 0.4s cubic-bezier(0.23, 1, 0.32, 1),
    background-color 200ms cubic-bezier(0.23, 1, 0.32, 1);

  @include from($tablet) {
    display: none;
  }
}

.sliddown {
  transform: translate(0, 64px);
}

.changecolor {
  background-color: $bm;
}

@media screen and (min-width: 768px) {
  .navbar,
  .navbar-menu,
  .navbar-start,
  .navbar-end {
    align-items: stretch;
    display: flex;
    background-color: transparent;
  }

  .navbar-menu {
    flex-grow: 1;
    flex-shrink: 0;
    padding: 4px 0;

    @include from($desktop) {
      padding: 4px 0;
    }
  }

  .navbar-end {
    justify-content: flex-end;
    margin-left: auto;
  }

  .navbar-burger {
    display: none;
  }
}
</style>
