<template>
  <div class="base-textarea">
    <label :for="name" v-if="label">{{ label }}</label>
    <textarea
      class="textarea body-2"
      :class="{ 'is-resizeable': isResizeable, }"
      :value="value"
      :name="name"
      :id="name"
      :placeholder="placeholder"
      :disabled="isDisabled"
      :cols="cols"
      :rows="rows"
      @input="onInput($event.target.value)"
      @focus="onFocus($event.target.value)"
      @blur="onBlur($event.target.value)"
    />
    <p class="error" v-show="error">{{ error }}</p>
  </div>
</template>

<script>
export default {
  name: "BaseTextarea",
  props: {
    label: String,
    name: String,
    placeholder: String,
    isDisabled: Boolean,
    cols: String,
    rows: String,
    error: String,
    isResizeable: { type: Boolean, default: false },
    value: String
  },

  methods: {
    onInput(value) {
      this.$emit("input", { value: value });
    },

    onFocus(value) {
      this.$emit("focus", { value: value });
    },

    onBlur(value) {
      this.$emit("blur", { value: value });
    },
  },
};
</script>

<style lang="scss" scoped>
.base-textarea {
  .textarea {
    padding: 10px 12px;
    background: $bh;
    border: none;
    outline: none;
    box-shadow: 0 0 0 1px transparent, 0 0 0 1px $wh-ov24 inset;
    color: $wh;
    caret-color: $p1;
    width: 100%;
    // min-width: 0;
    resize: none;

    &::placeholder {
      color: $wl;
    }

    &:hover {
      box-shadow: 0 0 0 1px transparent, 0 0 0 1px $wl inset;
    }

    &:focus {
      box-shadow: 0 0 0 1px $p3, 0 0 0 1px $p3 inset;
    }

    &:disabled {
      background-color: $wh-ov24;
      color: $wh-ov24;
      &:hover {
        box-shadow: 0 0 0 1px transparent, 0 0 0 1px $wh-ov24 inset;
      }
    }

    &.is-resizeable {
      resize: both;
    }
  }

  .error {
    color: $error;
    margin: 6px 0 0 0;
  }

  &.is-fullwidth {
    width: 100px;
    min-width: none;
  }
}
</style>