<template>
  <section id="workflow" class="columns">
    <!-- workflow -->
    <div class="title column is-6">
      <h4 class="h4">Easy workflow</h4>
    </div>

    <div class="mainbody column">
      <div class="columns">
        <!-- Text block -->
        <div class="column">
          <div class="text-block">
            <span class="h3 first">1</span>
            <h5 class="h5">Upload file</h5>
            <p class="body-1">Upload your file to BlackHole, and create a private-link to the file.</p>
          </div>
        </div>
        <!-- Gallery block-->
        <div class="gallery-block column is-6">
            <transition-group tag="div" :name="back ? 'slideback' : 'slide'" class="img-slider">
              <div v-for="(img, index) in imgListFilteredBy(this.os)" :key="index" v-show="tabName === img.tabName">
                <img ref="image" :src="img.src" :srcset="img.srcSet" :alt="img.alt" class="image"/>
              </div>
            </transition-group>

          <div class="navigation subtitle-1">
            <span
                v-for="(tab, index) in tabList"
                :key="index" class="items"
                :class="{active: tabName === tab.name}"
                @click="tabOnClick(tab.name, index)">
              {{ tab.name }}
            </span>
            <span class="movable-indicator" :class="position"></span>
          </div>
        </div>
        
      </div>
      <div class="columns">
        <div class="column">
          <div class="text-block">
            <span class="h3">2</span>
            <h5 class="h5">Send link</h5>
            <p class="body-1">Share the private-link with anyone to access the file – download without registration.</p>
          </div>
        </div>
        <div class="images-block column is-6">
          <img
              src="../assets/i_guid_win_copylink.png"
              alt="Copy and share the private-link with anyone to access the file"
              srcset="../assets/i_guid_win_copylink.png 1x, ../assets/i_guid_win_copylink_2x.png 2x"
          />
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      os: "",
      tabName: "Select",
      position: "",
      back: false,
      lastIndex: 0,
      tabList: [
        {name: 'Select'},
        {name: 'Drag and drop'},
        {name: 'Shortcut'},
      ],
      imgList: [
        {
          src: 'img/i_guid_win_upload.png',
          srcSet: 'img/i_guid_win_upload.png 1x, img/i_guid_win_upload_2x.png 2x',
          alt: 'Upload your file in BlackHole',
          tabName: 'Select',
          os: 'Windows',
        },
        {
          src: 'img/i_guid_win_drag.png',
          srcSet: 'img/i_guid_win_drag.png 1x, img/i_guid_win_drag_2x.png 2x',
          alt: 'Drag and drop your file into BlackHole tray icon',
          tabName: 'Drag and drop',
          os: 'Windows'
        },
        {
          src: 'img/i_guid_win_shortcut.png',
          srcSet: 'img/i_guid_win_shortcut.png 1x, img/i_guid_win_shortcut_2x.png 2x',
          alt: 'Use BlackHole shortcuts to make things easier',
          tabName: 'Shortcut',
          os: 'Windows'
        },

        {
          src: 'img/i_guid_mac_upload.png',
          srcSet: 'img/i_guid_mac_upload.png 1x, img/i_guid_mac_upload_2x.png 2x',
          alt: 'Upload your file in BlackHole',
          tabName: 'Select',
          os: 'Mac OS',
        },
        {
          src: 'img/i_guid_mac_drag.png',
          srcSet: 'img/i_guid_mac_drag.png 1x, img/i_guid_mac_drag_2x.png 2x',
          alt: 'Drag and drop your file into BlackHole tray icon',
          tabName: 'Drag and drop',
          os: 'Mac OS',
        },
        {
          src: 'img/i_guid_mac_shortcut.png',
          srcSet: 'img/i_guid_mac_shortcut.png 1x, img/i_guid_mac_shortcut_2x.png 2x',
          alt: 'Use BlackHole shortcuts to make things easier',
          tabName: 'Shortcut',
          os: 'Mac OS'
        },
      ]
    };
  },

  mounted() {
    this.getOS();
  },

  methods: {
    // osDetection() {
    //   this.os = "Windows";
    //   if (
    //       navigator.appVersion.indexOf("Windows" || "Linux" || "Android") != -1
    //   ) {
    //     this.os = "Windows";
    //     return;
    //   }

    //   if (navigator.appVersion.indexOf("Mac" || "iPad" || "iPhone") != -1) {
    //     this.os = "MacOS";
    //     return;
    //   }
    // },

    getOS() {
        let userAgent = window.navigator.userAgent,
            platform = window.navigator.platform,
            macosPlatforms = ['Macintosh', 'MacIntel', 'MacPPC', 'Mac68K'],
            windowsPlatforms = ['Win32', 'Win64', 'Windows', 'WinCE'],
            iosPlatforms = ['iPhone', 'iPad', 'iPod'];

        if (macosPlatforms.indexOf(platform) !== -1) {
            this.os = 'Mac OS';
        } else if (iosPlatforms.indexOf(platform) !== -1) {
            this.os = 'iOS';
        } else if (windowsPlatforms.indexOf(platform) !== -1) {
            this.os = 'Windows';
        } else if (/Android/.test(userAgent)) {
            this.os = 'Android';
        } else if (!os && /Linux/.test(platform)) {
            this.os = 'Linux';
        }
    },

    tabOnClick(tabName, index) {
      this.tabName = tabName;
      this.position = `pos${index}`;
      if (index - this.lastIndex >= 0) {
        this.back = false;
        this.lastIndex = index
      } else {
        this.back = true;
        this.lastIndex = index
      }
    },

    imgListFilteredBy: function (os) {
      return this.imgList.filter(function (img) {
        return img.os === os;
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@include override-gaps;

#workflow {
  padding: 160px 0 160px;
  flex-direction: column;

  @include until($tablet) {
    padding: 128px 0 128px;
  }

  .title {
    // columns has display:flex declaration
    align-self: center;

    h4 {
      margin: 0 16px 65px;
      color: $wh;
      @include until($tablet) {
        margin: 0 0 81px;
      }
    }
  }
}

.mainbody {
  @include until($tablet) {
    padding: 0;
  }

  .columns {
    margin: 0 0 101px;

    &:last-child {
      margin: 0;
    }

    @include until($tablet) {
      margin: 0 0 91px;
    }
  }
}

.text-block {
  width: 75%;
  margin: 0 0 0 25%;
  @include until($tablet) {
    width: 100%;
    margin: 0;
  }

  .h3 {
    display: inline-block;
    color: $bl;
    margin: 0 0 45px;
    @include until($tablet) {
      margin: 0 0 15px;
    }
  }

  .h5 {
    color: $wh;
    margin: 0 0 6px;
  }

  .body-1 {
    color: $wm;
    margin: 0 40px 20px 0;
    @include until($tablet) {
      margin: 0 0 23px;
    }
  }

  .first {
    margin: 20px 0 45px;
    @include until($tablet) {
      margin: 0 0 15px;
    }
  }
}

.gallery-block {
  .windows,
  .mac {
    max-width: 449px;
    height: 208px;
    overflow: hidden;
  }
}

.navigation {
  position: relative;
  max-width: 448px;
  padding: 20px 0 0;
  border-bottom: 1px solid fade-out($color: $wh, $amount: 0.92);

  .items {
    display: inline-block;
    color: $wl;
    margin: 0 33px 0 0;
    padding: 0 0 10px;
    cursor: pointer;
    transition: color 300ms cubic-bezier(0.23, 1, 0.32, 1);
    @include until($tablet) {
      margin: 0 24px 0 0;
      &:nth-child(3n) {
        margin: 0;
      }
    }

    &:nth-child(3n) {
      margin: 0;
    }
  }

  .active {
    color: $wm;
  }

  .movable-indicator {
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 57px;
    height: 2px;
    background-color: $wm;
    transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1),
    width 300ms cubic-bezier(0.23, 1, 0.32, 1);
  }

  .pos0 {
    transform: translate(0, 0);
    width: 54px;
    @include until($tablet) {
      transform: translate(0, 0);
    }
  }

  .pos1 {
    transform: translate(87px, 0);
    width: 123px;
    @include until($tablet) {
      transform: translate(78px, 0);
    }
  }

  .pos2 {
    transform: translate(243px, 0);
    width: 76px;
    @include until($tablet) {
      transform: translate(225px, 0);
    }
  }
}

.slide-enter-active, .slide-leave-active {
  transition: .3s cubic-bezier(0.23, 1, 0.32, 1);
}

.slide-enter {
  transform: translate(100%, 0);
}

.slide-leave-to {
  transform: translate(-100%, 0);
}

.slideback-enter-active, .slideback-leave-active {
  transition: .3s cubic-bezier(0.23, 1, 0.32, 1);
}

.slideback-enter {
  transform: translate(-100%, 0);
}

.slideback-leave-to {
  transform: translate(100%, 0);
}

.img-slider {
  overflow: hidden;
  position: relative;
  max-width: 449px;
  height: 208px;
}

.img-slider img {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  border-right: 1px solid transparent;
}
</style>
