export const BLOCKSTACK_GAIA_HUB_READ_BASE_URL = "https://gaia.blockstack.org/hub/";

export class BHCrypto {
    static split(buffer, from, to) {
        if (to) {
            return Buffer.from(Uint8Array.prototype.slice.call(buffer, from, to));
        } else {
            return Buffer.from(Uint8Array.prototype.slice.call(buffer, from));
        }
    }

    static pkcs7Decrypt(password, salt, iterations, content) {
        let key = crypto.pbkdf2Sync(Buffer.from(password, "utf-8"), Buffer.from(salt, "utf-8"), iterations, 32, "sha256");
        let iv = Buffer.from(Uint8Array.prototype.slice.call(content, 0, 16));
        let cipherText = Buffer.from(Uint8Array.prototype.slice.call(content, 16));
        return this.aesDecrypt(iv, key, cipherText);
    }

    static asyncPKCS7Decrypt(password, salt, iterations, content) {
        return new Promise((resolve, reject) => {
            window.crypto.subtle.importKey("raw", password, {
                name: "PBKDF2"
            }, false, ['deriveBits', 'deriveKey']).then(key => {
                return window.crypto.subtle.deriveKey({
                    name: "PBKDF2",
                    salt,
                    iterations,
                    hash: "SHA-256"
                }, key, {
                    "name": 'AES-CBC',
                    "length": 256
                }, true, ["encrypt", "decrypt"]);
            }).then(key => {
                return window.crypto.subtle.exportKey("raw", key);
            }).then((key) => {
                let iv = Buffer.from(Uint8Array.prototype.slice.call(content, 0, 16));
                let cipherText = Buffer.from(Uint8Array.prototype.slice.call(content, 16));
                resolve(this.aesDecrypt(iv, key, cipherText));
            }).catch((reason) => {
                reject(reason);
            });
        });
    }

    static pkcs7Encrypt(password, salt, iterations, content) {
        let key = crypto.pbkdf2Sync(Buffer.from(password, "utf-8"), Buffer.from(salt, "utf-8"), iterations, 32, "sha256");
        let iv = crypto.randomBytes(16);
        return this.aesEncrypt(iv, key, content);
    }

    static async asyncPKCS7Encrypt(password, salt, iterations, content) {
        return new Promise((resolve, reject) => {
            window.crypto.subtle.importKey("raw", password, {
                name: "PBKDF2"
            }, false, ['deriveBits', 'deriveKey']).then(key => {
                return window.crypto.subtle.deriveKey({
                    name: "PBKDF2",
                    salt,
                    iterations,
                    hash: "SHA-256"
                }, key, {
                    "name": 'AES-CBC',
                    "length": 256
                }, true, ["encrypt", "decrypt"]);
            }).then(key => {
                return window.crypto.subtle.exportKey("raw", key);
            }).then((key) => {
                let iv = crypto.randomBytes(16);
                resolve(this.aesEncrypt(iv, key, content));
            }).catch((reason) => {
                reject(reason);
            });
        });
    }

    static sha256(content) {
        return crypto.createHash('sha256').update(content).digest();
    }

    static aesEncrypt(iv, key, plaintext) {
        const cipher = crypto.createCipheriv('aes-256-cbc', key, iv)
        return Buffer.concat([iv, cipher.update(plaintext), cipher.final()])
    }

    static aesDecrypt(iv, key, ciphertext) {
        const cipher = crypto.createDecipheriv('aes-256-cbc', key, iv)
        return Buffer.concat([cipher.update(ciphertext), cipher.final()])
    }
}

export class BHGaiaProvider {
    get privateKey() {
        return (this.fPrivateKey) ? this.fPrivateKey.toString("hex") : undefined;
    }

    set privateKey(value) {
        if (!value)
            throw "Can't set key as undefined.";
        this.fPrivateKey = Buffer.from(value, "hex");
        if (!this.fPrivateKey)
            throw "Failed to make key from hex.";
        this.fPublicKey = ECPair.fromPrivateKey(this.fPrivateKey).publicKey;
    }

    get publicKey() {
        return (this.fPublicKey) ? this.fPublicKey.toString("hex") : undefined;
    }

    fHost = "";

    get host() {
        return this.fHost;
    }

    set host(value) {
        this.fHost = value;
    }

    fChallangeText = "";

    get challangeText() {
        return this.fChallangeText;
    }

    set challangeText(value) {
        this.fChallangeText = value;
    }

    fUrlPerfix = "";

    get urlPerfix() {
        return this.fUrlPerfix;
    }

    set urlPerfix(value) {
        this.fUrlPerfix = value;
    }

    fToken = "";

    get token() {
        return this.fToken;
    }

    fAddress = "";

    get address() {
        return this.fAddress;
    }

    getStoreURL(fileName) {
        return `${this.fHost}/store/${this.fAddress}/${fileName}`;
    }

    getReadURL(fileName) {
        if (this.fUrlPerfix.endsWith("/")) {
            return `${this.fUrlPerfix}${this.fAddress}/${fileName}`;
        }
        return `${this.fUrlPerfix}/${this.fAddress}/${fileName}`;
    }

    getDeleteURL(fileName) {
        return `${this.fHost}/delete/${this.fAddress}/${fileName}`;
    }

    getListFilesURL() {
        return `${this.fHost}/list-files/${this.fAddress}`;
    }

    makeAuthToken() {
        if (!this.fPublicKey || !this.fPrivateKey)
            throw "Can't make auth token without Public/Private key";

        let payload = {
            gaiaChallenge: this.fChallangeText,
            hubUrl: this.fHost,
            iss: this.fPublicKey.toString("hex"),
            salt: crypto.randomBytes(16)
        };
        this.fToken = "v1:" + (new TokenSigner("ES256K", this.fPrivateKey.toString("hex")).sign(payload));
        let pubHash = BCrypto.hash160(this.fPublicKey);
        this.fAddress = address.toBase58Check(pubHash, 0x00);
    }

    async getHubInfo() {
        let response = await axios.get(this.fHost + "/hub_info");
        let hubObject = response.data;
        this.fChallangeText = hubObject.challenge_text;
        this.fUrlPerfix = hubObject.read_url_prefix;
        this.makeAuthToken();
    }

    async prepare() {
        if (this.fUrlPerfix != "")
            this.makeAuthToken();
        else
            await this.getHubInfo();
    }

    async upload(fileName, content, contentType = "", onProgress) {
        if (contentType == "")
            contentType = "application/octet-stream";
        let dataObj = (await axios.post(this.getStoreURL(fileName), content, {
            responseType: "json",
            onUploadProgress: (progress) => {
                if (onProgress)
                    onProgress(progress.loaded, progress.total);
            },
            headers: {
                "Authorization": `bearer ${this.fToken}`,
                "Content-Type": contentType
            }
        })).data;
        return dataObj.URL; //return  key; currenty skip convertation
    }

    async delete(fileName) {
        await axios.delete(this.getDeleteURL(fileName), {
            headers: {
                "Authorization": `bearer ${this.fToken}`
            }
        });
    }

    async download(fileName) {
        let url = this.getReadURL(fileName);
        return new Uint8Array((await axios.get(url, {
            responseType: "arraybuffer"
        })).data);
    }

    async listFiles(page) {
        return (await axios.post(this.getListFilesURL(), JSON.stringify({
            page: page
        }), {
            headers: {
                "Authorization": `bearer ${this.fToken}`,
                "Content-Type": "application/json"
            },
            responseType: "json"
        })).data; //Skip  url
    }

    async drivenRandomHub(randomHub) {
        if (randomHub == undefined)
            randomHub = new BHGaiaProvider();
        let randomPriKey = ECPair.makeRandom().privateKey;
        await this.prepare()
        if (randomPriKey) {
            randomHub.privateKey = randomPriKey.toString("hex");
            randomHub.challangeText = this.fChallangeText;
            randomHub.host = this.fHost;
            randomHub.urlPerfix = this.fUrlPerfix;
        }
        return randomHub;
    }
}

