export default class BHUploader {

    isCompressed = false;
    fileSize = 0;
    compressedSize = 0;
    password = "";
    data = null;

    constructor(hubUrl, privateKey) {
        this.fUserHub = new BHGaiaProvider();
        this.fUserHub.host = hubUrl;
        this.fUserHub.privateKey = privateKey;
        this.fUserHub.prepare();
        this.fFileHub = new BHGaiaProvider();
        this.onStart = undefined;
        this.onProgress = undefined;
        this.onFinish = undefined;
        this.onCancel = undefined;
        this.onDelete = undefined;
        this.onRename = undefined;
        this.onPassword = undefined;
        this.canceled = false;
        this.url = undefined;
        this.isMobile = /Android|webOS|iPhone|iPad|Mac|Macintosh|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
    }

    setPassword(password) {
        this.password = password;
    }

    async uploadFileInfo(name, encryptionKey, fileInfo) {
        let info = JSON.stringify(fileInfo);
        let iv = crypto.randomBytes(16);
        let encryptedFile = BHCrypto.aesEncrypt(iv, encryptionKey, Buffer.from(pako.deflateRaw(info)));
        await this.fFileHub.upload(name, encryptedFile);
    }

    async upgradeHistory(historyFileName, oldHistory) {
        oldHistory.PAS = "";
        oldHistory.TS = Date.now();
        oldHistory.BHV = 8;
        oldHistory.FS = null;
        let hs = JSON.stringify(oldHistory);
        await storage.putFile(historyFileName, hs, {
            encrypt: true
        });
        return oldHistory;
    }

    async uploadHistory(fileName, size, encKey, shpk, shpa, password, historyFileName, oldHistory) {
        let history = {
            BHV: 8,
            PK: oldHistory ? oldHistory.PK : this.fFileHub.privateKey,
            FN: fileName,
            FS: size,
            KY: encKey.toString("hex"),
            SHPK: shpk,
            SHPA: shpa,
            PAS: password,
            TS: oldHistory ? oldHistory.TS : Date.now()
        };
        let hs = JSON.stringify(history);
        if (historyFileName) {
            await storage.putFile(historyFileName, hs, {
                encrypt: true
            });
        } else {
            historyFileName = randomBytes(4).toString("hex") + "_" + Date.now();
            await storage.putFile(historyFileName, hs, {
                encrypt: true
            });
        }
        return {
            HFN: historyFileName,
            H: history
        };
    }

    async upload(fileName) {
        this.url = null;
        if (this.canceled) {
            this.onCancel();
            return;
        }
        await this.fUserHub.drivenRandomHub(this.fFileHub);
        if (this.canceled) {
            this.onCancel();
            return;
        }
        await this.fFileHub.prepare();
        if (this.canceled) {
            this.onCancel();
            return;
        }
        let buffer = Buffer.from(this.data);

        let SHPK = ECPair.makeRandom();
        let SHPA = randomBytes(5).toString("hex");
        let SHHub = new BHGaiaProvider();
        SHHub.host = BLOCKSTACK_GAIA_HUB_STORE_URL;
        SHHub.privateKey = SHPK.privateKey.toString("hex");
        if (this.canceled) {
            this.onCancel();
            return;
        }
        SHHub.prepare();

        //For progress
        let fileSize = buffer.byteLength;
        let uploadedSize = 0;
        let lastUploadedSize = -16;

        if (this.onStart)
            this.onStart(fileName, this.fileSize);

        let partSize;
        let encryptionKey = crypto.randomBytes(32);
        let infoName = crypto.randomBytes(3).toString("hex");
        // if (buffer.byteLength > 5 * 1024 * 1024)
        //     partSize = 5 * 1024 * 1024;
        // else
        partSize = 256 * 1024;
        let file = {
            Size: this.fileSize,
            PartSize: partSize,
            Parts: [],
            Recipe: [],
            Name: fileName,
            Mime: ""
        };
        if (this.isCompressed) {
            file.Recipe.push('deflateRaw');
        }
        let partCount = Math.floor((fileSize + partSize - 1) / partSize);
        for (let i = 0; i < partCount; i++) {
            file.Parts[i] = "";
        }
        if (this.canceled) {
            this.onCancel();
            return;
        }
        await this.uploadFileInfo(infoName, encryptionKey, file);
        let urlData = JSON.stringify({
            BHV: 7,
            FN: this.fFileHub.getReadURL(infoName),
            KY: encryptionKey.toString("hex")
        });
        if (this.canceled) {
            this.onCancel();
            return;
        }
        if (this.password != "") {
            let salt = crypto.randomBytes(16);
            let encryotedUrlData = BHCrypto.pkcs7Encrypt(this.password, Buffer.from(salt).toString("hex"), 60000, Buffer.from(urlData));

            urlData = JSON.stringify({
                BHV: 7,
                SL: Buffer.from(salt).toString("hex"),
                CN: base64url(Buffer.from(encryotedUrlData))
            });
        }
        let encodedUrlData = new TextEncoder().encode(urlData);
        let compressedUrlData = pako.deflateRaw(encodedUrlData);
        let url = base64url(Buffer.from(compressedUrlData));
        if (this.canceled) {
            this.onCancel();
            return;
        }
        let shd = BHCrypto.pkcs7Encrypt(SHPA, SHHub.address, 60000, Buffer.from(url));
        if (this.canceled) {
            this.onCancel();
            return;
        }
        await SHHub.upload("url", shd);

        for (let i = 0; i < partCount; i++) {
            if (this.canceled) {
                this.onCancel();
                return;
            }
            let from = i * partSize;
            let to = (i + 1) * partSize;
            let partData = Uint8Array.prototype.slice.call(buffer, from, to);
            let iv = crypto.randomBytes(16);
            let encryptPartData = BHCrypto.aesEncrypt(iv, encryptionKey, Buffer.from(partData));
            file.Parts[i] = BHCrypto.sha256(encryptPartData).toString("hex");
            await this.fFileHub.upload(file.Parts[i], encryptPartData, "", (loaded) => {
                uploadedSize += (loaded - lastUploadedSize) * this.fileSize / fileSize;
                lastUploadedSize = loaded;
                if (this.canceled)
                    return;
                if (this.onProgress)
                    this.onProgress(uploadedSize, this.fileSize, uploadedSize === this.fileSize);
            });
            lastUploadedSize = -16;
            await this.uploadFileInfo(infoName, encryptionKey, file);
            if (!this.url)
                this.url = "https://app.blackhole.run/#" + SHPA + SHHub.address;
        }
        if (this.canceled) {
            this.onCancel();
            return;
        }
        if (this.canceled) {
            this.onCancel();
            return;
        }
        let history = await this.uploadHistory(fileName, fileSize, encryptionKey, SHPK.privateKey.toString("hex"), SHPA, this.password);
        if (this.onFinish)
            this.onFinish(this.url, history);
    }

    getURL(history) {
        let SHHub = new BHGaiaProvider();
        SHHub.host = BLOCKSTACK_GAIA_HUB_STORE_URL;
        SHHub.privateKey = history.SHPK.toString("hex");
        SHHub.makeAuthToken();
        return "https://app.blackhole.run/#" + history.SHPA + SHHub.address;
    }

    isCompressable(fileName, data) {

        if (this.isMobile && data.byteLength > 250 * 1024 * 1024)
            return false;

        const Ext = ['bmp', 'tif', 'tiff',
            'avi',
            'txt', 'json', 'xml'
        ];
        const CompExt = ['jpg', 'png', 'gif',
            'mp3', 'flac', 'wma', 'ogg',
            'mp4', 'mov', 'wmv', 'webp', 'mkv',
            'zip', 'rar', 'gz', 'tar', 'cab'
        ];

        const TestSize = 10 * 1024;

        let idx = fileName.lastIndexOf(".");
        if (idx >= 0) {
            let ext = fileName.substr(idx);
            if (Ext.indexOf(ext) > -1)
                return true;
            if (CompExt.indexOf(ext) > -1)
                return false;
        }

        if (data.byteLength < 2 * TestSize)
            return false;

        let testData = data.slice(0, TestSize);
        let ziped = pako.deflateRaw(testData);
        return ziped.byteLength * 1.0 / testData.byteLength > 0.1
    }

    recursiveReadFilesAndDirectoriesAndCompress(items, index, zip, callback) {
        if (items.length <= index) {
            callback();
            return;
        }
        let wke = items[index];
        if (wke.isDirectory) {
            var dirReader = wke.createReader();
            dirReader.readEntries((entries) => {
                let folder = zip.folder(wke.name);
                this.recursiveReadFilesAndDirectoriesAndCompress(entries, 0, folder, () => {
                    index++;
                    if (items.length > index)
                        this.recursiveReadFilesAndDirectoriesAndCompress(items, index, zip, callback);
                    else callback();
                });
            });
        } else {
            items[index].file((file) => {
                this.fileSize += file.size;
                if (this.fileSize > 536870912) {
                    callback({
                        fileName: file.name,
                        error: "File size limited to 512 MB.",
                        done: true
                    });
                    throw {
                        fileName: file.name,
                        error: "File size limited to 512 MB.",
                        done: true
                    };
                }
                let reader = new FileReader();
                reader.onloadend = event => {
                    if (this.isCompressable(file.name, event.target.result)) {
                        zip.file(file.name, Buffer.from(event.target.result), {
                            binary: true,
                            compression: "DEFLATE",
                            compressionOptions: {
                                level: 6
                            }
                        });
                    } else {
                        zip.file(file.name, Buffer.from(event.target.result), {
                            binary: true
                        });
                    }

                    index++;
                    if (items.length > index)
                        this.recursiveReadFilesAndDirectoriesAndCompress(items, index, zip, callback);
                    else callback();
                };
                reader.readAsArrayBuffer(file);
            });
        }
    }

    recursiveReadFilesAndDirectories(items, callback) {
        let wke = items[0].webkitGetAsEntry();
        if (items.length === 1 && wke.isFile) {
            let file = items[0].getAsFile();
            this.fileSize += file.size;
            if (this.fileSize > 536870912) {
                callback({
                    fileName: file.name,
                    error: "File size limited to 512 MB.",
                    done: true
                });
                throw {
                    fileName: file.name,
                    error: "File size limited to 512 MB.",
                    done: true
                };
            }
            if (this.isMobile) {
                let reader = new FileReader();
                reader.onloadend = event => {
                    if (this.isCompressable(file.name, event.target.result)) {
                        let worker = new CompressWorker();
                        worker.onmessage = (e) => {
                            this.data = e.data.data;
                            this.isCompressed = e.data.isCompressed;
                            this.fileSize = file.size;
                            callback({
                                fileName: file.name,
                                error: "",
                                buffer: Buffer.from(this.data),
                                done: true
                            });
                        };
                        worker.postMessage(event.target.result);
                    } else {
                        this.data = event.target.result;
                        this.fileSize = event.target.result.byteLength;
                        callback({
                            fileName: file.name,
                            error: "",
                            buffer: Buffer.from(this.data),
                            done: true
                        });
                    }
                };
                reader.readAsArrayBuffer(file);
            } else {
                let reader = new ReaderWorker();
                reader.onmessage = event => {
                    if (this.isCompressable(file.name, event.data)) {
                        let worker = new CompressWorker();
                        worker.onmessage = (e) => {
                            this.data = e.data.data;
                            this.isCompressed = e.data.isCompressed;
                            this.fileSize = file.size;
                            callback({
                                fileName: file.name,
                                error: "",
                                buffer: Buffer.from(this.data),
                                done: true
                            });
                        };
                        worker.postMessage(event.data);
                    } else {
                        this.data = event.data;
                        this.fileSize = file.size;
                        callback({
                            fileName: file.name,
                            error: "",
                            buffer: Buffer.from(this.data),
                            done: true
                        });
                    }
                };
                reader.postMessage(file);
            }
        } else {
            let nItems = [];
            for (let i = 0; i < items.length; i++) {
                nItems.push(items[i].webkitGetAsEntry());
            }
            try {
                let zip = new JSZip();
                this.recursiveReadFilesAndDirectoriesAndCompress(nItems, 0, zip, () => {
                    zip.generateAsync({
                        type: "arraybuffer"
                    }).then((data) => {
                        this.data = data;
                        this.fileSize = data.byteLength;
                        callback({
                            fileName: "Archive.zip",
                            error: "",
                            buffer: data,
                            done: true
                        });
                    });
                });
            } catch (e) {
                callback(e);
            }
        }
    }

    recursiveReadFilesAndCompress(items, index, zip, callback) {
        if (items.length <= index) {
            callback();
            return;
        }
        let file = items[index];
        let reader = new FileReader();
        this.fileSize += file.size;
        if (this.fileSize > 536870912) {
            callback({
                fileName: file.name,
                error: "File size limited to 512 MB.",
                done: true
            });
            throw {
                fileName: file.name,
                error: "File size limited to 512 MB.",
                done: true
            };
        }
        reader.onloadend = event => {
            let folder = zip;
            if (file.webkitRelativePath) {
                let pathParts = file.webkitRelativePath.split("/");
                pathParts.pop();
                pathParts.forEach((part) => {
                    folder = folder.folder(part);
                });
            }
            if (this.isCompressable(file.name, event.target.result)) {
                folder.file(file.name, Buffer.from(event.target.result), {
                    binary: true,
                    compression: "DEFLATE",
                    compressionOptions: {
                        level: 6
                    }
                });
            } else {
                folder.file(file.name, Buffer.from(event.target.result), {
                    binary: true
                });
            }

            index++;
            if (items.length > index)
                this.recursiveReadFilesAndCompress(items, index, zip, callback);
            else callback();
        };
        reader.readAsArrayBuffer(file);
    }

    recursiveReadFiles(items, callback) {
        if (items.length === 1) {
            let file = items[0];
            this.fileSize += file.size;
            if (this.fileSize > 536870912) {
                callback({
                    fileName: file.name,
                    error: "File size limited to 512 MB.",
                    done: true
                });
                throw {
                    fileName: file.name,
                    error: "File size limited to 512 MB.",
                    done: true
                };
            }

            if (this.isMobile) {
                let reader = new FileReader();
                reader.onloadend = event => {
                    if (this.isCompressable(file.name, event.target.result)) {
                        let worker = new CompressWorker();
                        worker.onmessage = (e) => {
                            this.data = e.data.data;
                            this.isCompressed = e.data.isCompressed;
                            this.fileSize = file.size;
                            callback({
                                fileName: file.name,
                                error: "",
                                buffer: Buffer.from(this.data),
                                done: true
                            });
                        };
                        worker.postMessage(event.target.result);
                    } else {
                        this.data = event.target.result;
                        this.fileSize = event.target.result.byteLength;
                        callback({
                            fileName: file.name,
                            error: "",
                            buffer: Buffer.from(this.data),
                            done: true
                        });
                    }
                };
                reader.readAsArrayBuffer(file);
            } else {
                let reader = new ReaderWorker();
                reader.onmessage = event => {
                    if (this.isCompressable(file.name, event.data)) {
                        let worker = new CompressWorker();
                        worker.onmessage = (e) => {
                            this.data = e.data.data;
                            this.isCompressed = e.data.isCompressed;
                            this.fileSize = file.size;
                            callback({
                                fileName: file.name,
                                error: "",
                                buffer: Buffer.from(this.data),
                                done: true
                            });
                        };
                        worker.postMessage(event.data);
                    } else {
                        this.data = event.data;
                        this.fileSize = file.size;
                        callback({
                            fileName: file.name,
                            error: "",
                            buffer: Buffer.from(this.data),
                            done: true
                        });
                    }
                };
                reader.postMessage(file);
            }
        } else {
            try {
                let zip = new JSZip();
                this.recursiveReadFilesAndCompress(items, 0, zip, () => {

                    zip.generateAsync({
                        type: "arraybuffer"
                    }).then((data) => {
                        this.data = data;
                        this.fileSize = data.byteLength;
                        callback({
                            fileName: "Archive.zip",
                            error: "",
                            buffer: data,
                            done: true
                        });
                    });
                });
            } catch (e) {
                callback(e);
            }
        }
    }

    async processDragedFiles(input, callback) {
        // console.log("Dragged");
        let hasFile = (input.items && input.items.length > 0) || (input.files && input.files.length > 0);
        if (!hasFile)
            return {
                fileName: "",
                error: "",
                done: false
            };
        let processPrecent = 0;
        if (this.onProgress) {
            // console.log("Progress");
            this.onProgress(processPrecent, 100, true);
            let timer = setInterval(() => {
                processPrecent += 9;
                if (processPrecent > 90) {
                    clearInterval(timer);
                    return;
                }
                this.onProgress(processPrecent, 100, true);
            }, 2000);
        }
        let callbackCaller = (result) => {
            processPrecent = 100;
            callback(result);
        }
        if (input.items && input.items.length > 0 && isFunction(input.items[0].webkitGetAsEntry)) {
            return this.recursiveReadFilesAndDirectories(input.items, callbackCaller);
        } else if (input.files && input.files.length > 0) {
            return this.recursiveReadFiles(input.files, callbackCaller);
        } else {
            return {
                fileName: "",
                error: "",
                done: false
            };
        }
    }

    async deleteFile(HFN, history) {
        let hub = new BHGaiaProvider();
        hub.privateKey = history.PK;
        hub.host = this.fUserHub.host;
        await hub.prepare();
        let nextPage = null;
        do {
            let page = await hub.listFiles(nextPage);
            if (!page.entries)
                break;
            for (let i = 0; i < page.entries.length; i++) {

                await hub.delete(page.entries[i]);
            }
            nextPage = page.page;
        } while (nextPage);

        hub = new BHGaiaProvider();
        hub.privateKey = history.SHPK;
        hub.host = BLOCKSTACK_GAIA_HUB_STORE_URL;
        await hub.prepare();
        nextPage = null;
        do {
            let page = await hub.listFiles(nextPage);
            if (!page.entries)
                break;
            for (let i = 0; i < page.entries.length; i++) {
                await hub.delete(page.entries[i]);
            }
            nextPage = page.page;
        } while (nextPage);

        this.onDelete(HFN)
    }

    async renameFile(HFN, history, newName) {
        let hub = new BHGaiaProvider();
        hub.privateKey = history.PK;
        hub.host = this.fUserHub.host;
        await hub.prepare();
        let nextPage = null;
        let fn = "";
        do {
            let page = await hub.listFiles(nextPage);
            if (!page.entries)
                break;
            for (let i = 0; i < page.entries.length; i++) {
                if (page.entries[i].length == 6) {
                    fn = page.entries[i];
                    break;
                }
            }
            nextPage = page.page;
        } while (nextPage);

        if (fn) {
            let fi = await hub.download(fn);
            fi = BHCrypto.aesDecrypt(Buffer.from(fi.slice(0, 16)), Buffer.from(history.KY, "hex"), Buffer.from(fi.slice(16)));
            fi = pako.inflateRaw(fi);
            fi = Buffer.from(fi).toString("utf8");
            fi = JSON.parse(fi);
            fi.Name = newName;
            fi = JSON.stringify(fi);
            fi = pako.deflateRaw(fi);
            fi = BHCrypto.aesEncrypt(crypto.randomBytes(16), Buffer.from(history.KY, "hex"), Buffer.from(fi));
            await hub.upload(fn, fi);
            await this.uploadHistory(newName, history.FS, history.KY, history.SHPK, history.SHPA, history.PAS, HFN, history);

        }
        this.onRename(HFN);
    }

    async changePassword(HFN, history, newPassword) {

        let tempHub = new BHGaiaProvider();
        let pkW = new Pkcs7EncryptWorker();
        tempHub.host = this.fUserHub.host;
        tempHub.privateKey = history.PK;
        await tempHub.prepare();


        let nextPage = null;
        let fn = "";
        do {
            let page = await tempHub.listFiles(nextPage);
            if (!page.entries)
                break;
            for (let i = 0; i < page.entries.length; i++) {
                if (page.entries[i].length == 6) {
                    fn = page.entries[i];
                    break;
                }
            }
            nextPage = page.page;
        } while (nextPage);


        let hub = new BHGaiaProvider();
        hub.host = BLOCKSTACK_GAIA_HUB_STORE_URL;
        hub.privateKey = history.SHPK;
        await hub.prepare();

        let urlData = JSON.stringify({
            BHV: 7,
            FN: tempHub.getReadURL(fn),
            KY: history.KY.toString("hex")
        });

        let finalize = async () => {
            let encodedUrlData = new TextEncoder().encode(urlData);
            let compressedUrlData = pako.deflateRaw(encodedUrlData);
            let url = base64url(Buffer.from(compressedUrlData));
            pkW.onmessage = async (e) => {
                let shd = e.data;
                await hub.upload("url", shd);
                await this.uploadHistory(history.FN, history.FS, history.KY, history.SHPK, history.SHPA, newPassword, HFN, history);
                this.onPassword(HFN);
            }
            pkW.postMessage({password: history.SHPA, salt: hub.address, iterations: 60000, content: Buffer.from(url)});
        }

        if (newPassword != "") {
            let salt = crypto.randomBytes(16);
            pkW.onmessage = async (e) => {
                let encryotedUrlData = e.data;
                urlData = JSON.stringify({
                    BHV: 7,
                    SL: Buffer.from(salt).toString("hex"),
                    CN: base64url(Buffer.from(encryotedUrlData))
                });
                await finalize();
            }
            BHCrypto.pkcs7Encrypt(newPassword, Buffer.from(salt).toString("hex"), 60000, Buffer.from(urlData))
            pkW.postMessage({
                password: newPassword,
                salt: Buffer.from(salt).toString("hex"),
                iterations: 60000,
                content: Buffer.from(urlData)
            });
        } else {
            await finalize();
        }
    }
}
