<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
export default {
  methods: {},

  mounted() {
    fetch("https://analytics.arcane.ac/api/collect", {
      method: "POST",
      mode: "cors",
      cache: "no-cache",
      headers: {
        "Content-Type": "application/x-www-form-urlencoded"
      },
      body:
        "data=" +
        JSON.stringify({
          uid: "ec637ed8-66bb-435b-932c-c789f409491d",
          url: window.location.href,
          referrer: document.referrer
        })
    });
    setTimeout(() => {
      fetch("https://analytics.arcane.ac/api/valid", {
        method: "POST",
        mode: "cors",
        cache: "no-cache",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
        body:
          "data=" +
          JSON.stringify({
            uid: "ec637ed8-66bb-435b-932c-c789f409491d",
            url: window.location.href,
            referrer: document.referrer
          })
      });
    }, 15000);
  }
};
</script>

<style lang="scss">
button,
input[type="button"] {
  background-color: transparent;
  border: none;
  outline: none;
  padding: 0;
  margin: 0;
  cursor: pointer;
}

