<template>
  <img :src="dataUrl" :srcset="srcset" :alt="alt" />
</template>

<script>
import axios from "axios";
export default {
  name: "fetch-image",

  props: {
    src: String,
    srcset: String,
    alt: String
  },

  data() {
    return {
      dataUrl: ""
    };
  },

  methods: {
    downloadImage(src) {
      if (src.startsWith("http")) {
        axios.get(src, { responseType: "blob" }).then(res => {
          let reader = new FileReader();
          reader.onload = e => {
            this.$nextTick(() => {
              this.dataUrl = e.target.result;
            });
          };
          reader.readAsDataURL(res.data);
        });
      } else {
        this.dataUrl = src;
      }
    }
  },

  mounted() {
    this.downloadImage(this.src);
  },

  watch: {
    src(newSrc, oldSrc) {
      if (!this) return;
      if (newSrc !== oldSrc) this.downloadImage(newSrc);
    }
  }
};
</script>