/* eslint-disable */
<template>
  <!-- Hero section -->
  <section class="columns hero">
    <div class="column is-12">
      <div class="columns">
        <!-- left side -->
        <div class="column is-6 is-hidden-mobile">
          <div class="img-block">
            <div class="hero-gradient"></div>
            <img
              src="../assets/i_hero.png"
              srcset="../assets/i_hero.png 1x, ../assets/i_hero_2x.png 2x"
              alt="BlackHole Desktop User Interface"
            />
          </div>
        </div>

        <!-- right side -->
        <div class="column right">
          <h1 class="h2 h3-mobile">Share any file.<br class="is-hidden-mobile" />Fast and private.</h1>
          <p class="subtitle-1">Move your files around the web with high-performance file transfer service that secured with blockchain technology.</p>
          <ul class="h5">
            <li v-for="(feature, index) in featuresList" :key="index">
              <img
                src="../assets/done_24px.png"
                srcset="../assets/done_24px.png 1x, ../assets/done_24px_2x.png 2x"
                alt="Done icon"
              />
              <span>{{feature.item}}<sup v-show="feature.footnote">&#8239;{{feature.footnote}}</sup></span>
            </li>
          </ul>

          <!-- <router-link to="/select-os" @click.native="getBlackHoleButton" class="button-1">get blackhole</router-link> -->
          <!-- Sign up -->
          <div class="register" @keyup.enter="signUpbyEmail" v-show="isVisible">
            <input
              v-model="email"
              type="email"
              id="EMAIL"
              name="EMAIL"
              autocomplete="off"
              class="body-2"
              placeholder="Enter your email address"
              data-required="true"
              required
            />
            <div class="button-block">
              <input
                type="button"
                @click="signUpbyEmail"
                class="button-2"
                :class="{ loading: isAnimate}"
                :disabled="isAnimate"
                :value="signValue"
              />
              <span :class="{ spiner: isAnimate }"></span>
            </div>
            <span class="error">{{ errors }}</span>
          </div>
          <router-link to="/web" class="button-1" v-show="!isVisible">go to app</router-link>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import axios from "axios";
import { userSession } from "../user.js";
export default {
  data() {
    return {
      email: "",
      errors: "",
      clickTime: 0,
      currentTime: 0,
      isAnimate: false,
      signValue: "sign up",
      isVisible: true,
      featuresList:[
        {item: 'No storage limit', footnote: 1, id:1},
        {item: 'No link expiration',footnote: 0, id:2},
        {item: 'Download without registration',footnote: 0, id:3}
      ],
    };
  },

  mounted() {
    this.isVisible = !userSession.isUserSignedIn();
  },

  methods: {
    getBlackHoleButton() {
      this.$ga.event({
        eventCategory: "Router",
        eventAction: "from_home_hero_to_select_os",
        eventLabel: "GetBlackHoleButton",
        eventValue: 0
      });
    },

    validEmail(email) {
      const regex = /^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i;
      return regex.test(email);
    },

    signUpbyEmail() {
      if (!this.email) {
        this.$router.push("/sign-up");
      } else if (!this.validEmail(this.email)) {
        this.errors = "Invalid email address.";
      } else {
        this.clickTime = new Date().getTime();
        this.isAnimate = true;
        let formData = {
          EMAIL: this.email,
          email_address_check: "",
          locale: "en",
          html_type: "simple"
        };
        const encodeForm = data => {
          return Object.keys(data)
            .map(
              key =>
                encodeURIComponent(key) + "=" + encodeURIComponent(data[key])
            )
            .join("&");
        };
        axios
          .post(
            "https://sibforms.com/serve/MUIEAKiimd6PFpWulB8i7UIh53EuNfIfnRrevW7LpfY6Am6VUuYmxTw7veGEYKtBf22_7PsqGFqlH3Uj8STiIScQPWAtC8qp4nEtr-yC-MYeWdDSzIjPIvkmnFlBr00GNgxvHneVDV3mJ2QWn-3Kpjxo6E1SPLfE7VZ0yOSqk7tOcZPKgabovaXOcNYE79qquPT1iv2_8S6TCGCS",
            encodeForm(formData)
          )
          .then(() => {
            this.currentTime = new Date().getTime();
            const timeDiffrence = this.currentTime - this.clickTime;

            if (timeDiffrence > 1000) {
              setTimeout(() => {
                this.isAnimate = false;
                this.isDone = true;
                this.$router.push("/sign-up");
              }, timeDiffrence);
            } else {
              this.isAnimate = true;
              setTimeout(() => {
                this.isAnimate = false;
                this.isDone = true;
                this.$router.push("/sign-up");
              }, 1000);
            }
          })
          .catch(reason => {
            this.currentTime = new Date().getTime();
            const timeDiffrence = this.currentTime - this.clickTime;

            if (timeDiffrence > 1000) {
              setTimeout(() => {
                this.isAnimate = false;
                this.isDone = true;
                this.$router.push("/sign-up");
              }, timeDiffrence);
            } else {
              this.isAnimate = true;
              setTimeout(() => {
                this.isAnimate = false;
                this.isDone = true;
                this.$router.push("/sign-up");
              }, 1000);
            }
          });
      }
    }
  },
  watch: {
    email: function(newVal, oldVal) {
      if (this.email && this.errors === "Email required.") {
        this.errors = "";
      } else if (
        (!this.email && this.errors === "Invalid email address.") ||
        this.validEmail(this.email)
      ) {
        this.errors = "";
      }
    }
  }
};
</script>


<style lang="scss" scoped>
@include override-gaps;

// Hero section
.hero {
  padding: 80px 0 160px;

  @include until($tablet) {
    padding: 64px 0 192px;
  }
}

.img-block {
  position: relative;
  z-index: -1;

  img {
    position: absolute;
    min-width: 1920px;
    min-height: 1080px;
    left: -448px;
    top: -360px;
    mask-image: linear-gradient(
      90deg,
      rgba(24, 25, 26, 1) 0.15%,
      rgba(24, 25, 26, 1) 16.8%,
      rgba(24, 25, 26, 0.05) 58.93%,
      rgba(24, 25, 26, 0.05) 99.35%
    );
  }
}

.right {
  display: flex;
  flex-direction: column;

  h1 {
    color: $wh;
    margin: -6px 0 16px;
  }

  p {
    color: $wm;
    margin: 0 0 92px;
    width: 75%;
    @include until($tablet) {
      width: 100%;
      margin: 0 0 70px;
    }
  }

  ul {
    color: $wh;
    margin: 0 0 62px;
    @include until($tablet) {
      margin: 0 auto 48px;
    }

    li {
      margin: 0 0 32px;
      display: flex;
      @include until($tablet) {
        margin: 0 0 32px 0;
      }

      &:last-child {
        margin: 0;
        @include until($tablet) {
          margin: 0;
        }
      }
      
      img {
        width: 24px;
        height: 24px;
        margin: 0 20px -3px 0;
      }

      sup{
        font-size: 13px;
        line-height: 16px;
      }
    }
  }

  .button-1 {
    padding: 14px 32px 12px;
    text-align: center;
    background-color: $p1;
    color: $bh;
    // fixing the button to the bottom
    align-self: flex-start;
    transition: background-color 150ms cubic-bezier(0.645, 0.045, 0.355, 1);

    &:hover {
      background-color: $hover1;
    }

    &:active {
      background-color: $press1;
    }

    @include until($tablet) {
      margin: auto auto 0 calc((100% - 197px) / 2 + 6px);
    }
  }
}

.register {
  display: flex;
  flex-wrap: wrap;
  width: 80%;
  max-width: 384px;
  @include until($tablet) {
    width: 100%;
  }
}

.body-2 {
  max-width: calc(100% - 134px);
  padding: 0 43px 0 16px;
  background-color: transparent;
  border: 1px solid $bl;
  color: $wh;
  outline: none;
  @include until($tablet) {
    width: calc(100% - 103px);
    padding: 0 3px 0 16px;
    max-width: 263px;
  }
}

.button-2 {
  padding: 15px 37px 13px 36px;
  background-color: $p1;
  color: $bh;
  border: none;
  outline: none;
  transition: background-color 150ms cubic-bezier(0.645, 0.045, 0.355, 1);

  &:hover {
    background-color: $hover1;
  }

  &:active {
    background-color: $press1;
  }

  @include until($tablet) {
    padding: 14px 24px 14px 24px;
  }
}

.button-block {
  position: relative;
}

.loading {
  color: transparent;
  background-color: $loading;

  &:hover {
    background-color: $loading;
  }
}

.spiner {
  border: 2px solid transparent;
  border-top-color: $bh;
  border-right-color: $bh;
  border-radius: 50%;
  width: 1.5em;
  height: 1.5em;
  animation: spin 1s linear infinite;
  position: absolute;
  top: calc(50% - 0.75rem);
  left: calc(50% - 0.75rem);
  z-index: 2;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.done {
  color: transparent;
  background-color: $hover1;

  &:hover {
    background-color: $hover1;
  }
}

.error {
  color: $error;
  margin: 7px 0 0;
  flex-basis: 100%;
}
</style>
