<template>
  <div
    class="tooltip-parent"
    @mouseenter="onMouseEnter($event)"
    @mouseleave="onMouseLeave($event)"
  >
    <slot></slot>
    <div class="tooltip body-3" :class="[setVariant, position]" v-show="isVisible">
      <slot name="title"></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: String,
    isVisible: Boolean,
    variant: { type: String, default: "primary" },
    position: { type: String, default: "top-left" },
  },

  methods: {
    onMouseEnter(event) {
      this.$emit("mouseenter", event);
    },
    onMouseLeave(event) {
      this.$emit("mouseleave", event);
    },
  },

  computed: {
    setVariant() {
      if (this.variant === "primary") {
        return "primary";
      } else {
        return "secondary";
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.tooltip-parent {
  position: relative;
  .tooltip {
    position: absolute;
    padding: 12px 20px 15px;
    min-width: 264px;
    &::before {
      content: "";
      position: inherit;
      width: 8px;
      height: 8px;
      border-bottom: 4px solid transparent;
      border-left: 4px solid transparent;
    }

    span {
      vertical-align: baseline;
    }
  }

  .primary {
    background: $p1;
    color: $bl-high;
    &::before {
      border-top: 4px solid $p1;
      border-right: 4px solid $p1;
    }
  }

  .secondary {
    background: $s1;
    color: $bl-high;
    &::before {
      border-top: 4px solid $s1;
      border-right: 4px solid $s1;
    }
  }

  .top-left {
    bottom: calc(100% + 12px);
    left: calc(50% - 26px);
    &::before {
      bottom: -4px;
      left: 20px;
      transform: rotate(135deg);
    }
  }

  .top-right {
    bottom: calc(100% + 12px);
    right: calc(50% - 26px);
    &::before {
      bottom: -4px;
      right: 20px;
      transform: rotate(135deg);
    }
  }

  .bottom-left {
    top: calc(100% + 12px);
    left: calc(50% - 26px);
    &::before {
      top: -4px;
      left: 20px;
      transform: rotate(-45deg);
    }
  }

  .bottom-right {
    top: calc(100% + 12px);
    right: calc(50% - 26px);
    &::before {
      top: -4px;
      right: 20px;
      transform: rotate(-45deg);
    }
  }
}
</style>