<template>
  <div id="home" :class="{ padding: isAdded }">
    <div class="background-color">
      <div class="radial-gradient">
        <div class="dithering"></div>
      </div>
    </div>
    <div class="fixed">
      <!-- <ph-banner @editStyle="editHomeStyle"></ph-banner> -->
      <the-navbar :visibleButton="visibleNavbar"></the-navbar>
    </div>
    <router-view></router-view>
    <the-footer :visibleFooter="visibleFooter"></the-footer>
  </div>
</template>

<script>
import TheNavbar from "../components/TheNavbar";
import TheFooter from "../components/TheFooter";
// import ProductHuntBanner from "../components/ProductHuntBanner";

export default {
  components: {
    "the-navbar": TheNavbar,
    "the-footer": TheFooter,
    // "ph-banner": ProductHuntBanner
  },

  data() {
    return {
      isAdded: false,
      visibleNavbar: true,
      visibleFooter: true,
    };
  },
  mounted() {
    if (
      this.$route.path == "/login" ||
      this.$route.path == "/sign-up" ||
      this.$route.path == "/select-os" ||
      this.$route.path == "/download"
    ) {
      this.visibleNavbar = false;
    } else {
      this.visibleNavbar = true;
    }

    if (this.$route.path == "/login" || this.$route.path == "/sign-up" || this.$route.path == "/select-os" || this.$route.path == "/download") {
      this.visibleFooter = false;
    } else {
      this.visibleFooter = true;
    }
  },

  watch: {
    $route(to, from) {
      if (
        to.path == "/login" ||
        to.path == "/sign-up" ||
        to.path == "/select-os" ||
        to.path == "/download"
      ) {
        this.visibleNavbar = false;
      } else {
        this.visibleNavbar = true;
      }

      if (to.path == "/login" || to.path == "/sign-up" || to.path == "/select-os" || to.path == "/download") {
        this.visibleFooter = false;
      } else {
        this.visibleFooter = true;
      }
    },
  },
  methods: {
    editHomeStyle(boolean) {
      this.isAdded = boolean;
    },
  },
};
</script>

<style lang="scss">
#home {
  position: relative;
  overflow: hidden;
  transition: padding 450ms cubic-bezier(0.23, 1, 0.32, 1);

  .fixed {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 30;
  }

  .background-color {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: $bh;
    z-index: -1;

    .radial-gradient {
      position: absolute;
      width: 2244px;
      height: 2244px;
      left: 368px;
      top: 244px;
      @include until($tablet) {
        left: -127px;
        top: 44px;
      }

      background: radial-gradient(
        1122px at 50% 50%,
        $p2 0%,
        $p3 37.5%,
        $p4 70.83%,
        $bh 100%
      );
      filter: opacity(0.25);

      .dithering {
        position: absolute;
        background-image: radial-gradient(
            1122px at 50% 50%,
            transparent 90%,
            $bh 100%
          ),
          url("../assets/noisy_texture_150px.png");
        width: 100%;
        height: 100%;
      }
    }
  }
}

.padding {
  padding: 131px 0 0;
  @include until($tablet) {
    padding: 158px 0 0 0;
  }
}
</style>
