<template>
  <div class="button-block" @click="onClick">
    <router-link
      v-if="to"
      :to="to"
      class="base-button button-2"
      :class="[variant, { 'is-fullwidth': isFullwidth }, { 'has-img': hasImg }]"
      :disabled="isDisabled"
    >
      <slot></slot>
    </router-link>
    <a
      v-else-if="href"
      :href="href"
      class="base-button button-2"
      :class="[variant, { 'is-fullwidth': isFullwidth }, { 'has-img': hasImg }]"
      :disabled="isDisabled"
    >
      <slot></slot>
    </a>
    <button
      v-else
      class="base-button button-2"
      :class="[variant, { 'is-fullwidth': isFullwidth }, { 'has-img': hasImg }]"
      :disabled="isDisabled"
    >
      <slot></slot>
    </button>
  </div>
</template>

<script>
export default {
  name: "BaseButton",
  props: {
    to: String,
    href: String,
    variant: { type: String, default: "primary" },
    isDisabled: Boolean,
    isFullwidth: Boolean,
    hasImg: Boolean,
  },

  methods: {
    onClick() {
      this.$emit("click");
    },
  },
};
</script>

<style lang="scss" scoped>
.button-block {
  display: flex;
  .base-button {
    cursor: pointer;
    position: relative;
    padding: 15px 24px 13px;
    background-color: transparent;
    border: none;
    outline: none;
    user-select: none;

    display: flex;
    align-items: center;
    justify-content: center;

    &::after {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-color: transparent;
      transition: all 150ms cubic-bezier(0.37, 0, 0.63, 1);
    }

    &.primary {
      background-color: $p1;
      color: $bh;

      &:hover {
        &::after {
          background-color: $wh-ov24;
        }
      }

      &:active {
        &::after {
          background-color: $bl-ov24;
        }
      }

      ::v-deep svg {
        fill: $bh;
      }
    }

    &.secondary {
      background-color: $wh-ov08;
      color: $wm;

      &:hover {
        &::after {
          background-color: $wh-ov12;
        }
      }

      &:active {
        &::after {
          background-color: $bl-low;
        }
      }

      ::v-deep svg {
        fill: $wm;
      }
    }

    &:disabled {
      background-color: $wl;
      color: $bl-high;
      cursor: default;

      &:hover {
        &::after {
          background-color: transparent;
        }
      }

      &:active {
        &::after {
          background-color: transparent;
        }
      }
    }

    &.is-fullwidth {
      flex: 1;
    }

    &.has-img {
      padding: 8px 12px 8px;
      &::v-deep span {
        flex: 1;
        text-align: center;
        white-space: nowrap;
      }
    }
  }
}
</style>