<template>
  <div class="columns is-centered">
    <div class="welcome column is-6">
      <div class="centering">
        <h4 class="h4 h5-mobile">Welcome to BlackHole.</h4>
        <ul>
          <li>
            <img
                src="../assets/i_done_p1_24px.png"
                alt="Done icon"
                srcset="../assets/i_done_p1_24px.png 1x, ../assets/i_done_p1_24px_2x.png 2x"
            />
            <span class="body-1 body-2-mobile">Unlimited storage to share your files.</span>
          </li>
          <li>
            <img
                src="../assets/i_done_p1_24px.png"
                alt="Done icon"
                srcset="../assets/i_done_p1_24px.png 1x, ../assets/i_done_p1_24px_2x.png 2x"
            />
            <span class="body-1 body-2-mobile">Save time with Smart Compression.</span>
          </li>
          <li>
            <img
                src="../assets/i_done_p1_24px.png"
                alt="Done icon"
                srcset="../assets/i_done_p1_24px.png 1x, ../assets/i_done_p1_24px_2x.png 2x"
            />
            <span class="body-1 body-2-mobile">Keep your personal information private.</span>
          </li>
        </ul>
        <p class="caption">
          To continue, please accept our
          <router-link to="/legal" class="legal-link">Terms of Service</router-link>
          . We use cookies for functional and analytical purposes. We do not sell your data.
        </p>
        <router-link to="/web" class="accept-btn button-2">accept & continue</router-link>
      </div>
    </div>
  </div>
</template>

<script>
import {userSession} from "../user";
import {getAddressFromDID} from "blockstack";
import {decodeToken} from "jsontokens";
import {
  doPublicKeysMatchIssuer,
  doSignaturesMatchPublicKeys,
  isExpirationDateValid,
  isIssuanceDateValid
} from "blockstack";
import {decryptPrivateKey} from "blockstack/lib/auth/authMessages";
import {hexStringToECPair} from "blockstack";

export default {
  methods: {
    handleAuth: async () => {
      if (userSession.isSignInPending()) {
        let sessionData = userSession.store.getSessionData();
        const authResponseToken = userSession.getAuthResponseToken();
        const tokenPayload = decodeToken(authResponseToken).payload;
        const isValid = await Promise.all([
          isExpirationDateValid(authResponseToken),
          isIssuanceDateValid(authResponseToken),
          doSignaturesMatchPublicKeys(authResponseToken),
          doPublicKeysMatchIssuer(authResponseToken),
        ]);
        if (!isValid.every(val => val))
          return;
        console.log(tokenPayload);
        let appPrivateKey = tokenPayload.private_key
        let coreSessionToken = tokenPayload.core_token
        const transitKey = sessionData.transitKey;
        let hubUrl = tokenPayload.hubUrl
        let gaiaAssociationToken = tokenPayload.associationToken
        if (transitKey !== undefined && transitKey != null) {
          if (appPrivateKey !== undefined && appPrivateKey !== null) {
            try {
              appPrivateKey = await decryptPrivateKey(transitKey, appPrivateKey)
            } catch (e) {
              console.log(e);
              try {
                hexStringToECPair(tokenPayload.private_key)
              } catch (ecPairError) {
                console.log(ecPairError);
                return;
              }
            }
          }
          if (coreSessionToken !== undefined && coreSessionToken !== null) {
            try {
              coreSessionToken = await decryptPrivateKey(transitKey, coreSessionToken)
            } catch (e) {
            }
          }
        }

        sessionData.userData = {
          username: tokenPayload.username,
          profile: tokenPayload.profile,
          email: tokenPayload.email,
          decentralizedID: tokenPayload.iss,
          identityAddress: getAddressFromDID(tokenPayload.iss),
          appPrivateKey,
          coreSessionToken,
          authResponseToken,
          hubUrl,
          coreNode: tokenPayload.blockstackAPIUrl,
          gaiaAssociationToken
        };
        userSession.store.setSessionData(sessionData);
        // userSession.handlePendingSignIn().then(() => {
        // });
      }
    }
  },

  mounted() {
    this.handleAuth()
  }
};
</script>

<style lang="scss" scoped>
@include override-gaps;
.centering {
  width: auto;
  max-width: 500px;
  margin: 0 auto;
}

.welcome {
  @include until($tablet) {
    height: 100%;
    min-height: 625px;
    display: flex;
    align-items: center;
  }

  .h4 {
    color: $wh;
    margin: 0 0 24px;
  }

  ul {
    margin: 0 0 57px;
    @include until($tablet) {
      margin: 0 0 48px;
    }

    li {
      display: flex;
      align-items: center;
      margin: 0 0 17px;
      @include until($tablet) {
        margin: 0 0 14px;
      }

      .body-1 {
        color: $wh;
      }

      img {
        width: 24px;
        height: 24px;
        margin: -3px 16px 0 0;
      }
    }
  }
}

.caption {
  color: $wl;
  margin: 0 0 24px;
}

.accept-btn {
  padding: 14px 24px;
  background-color: $p1;
  color: $bh;
  display: inline-block;
}

.logout {
  color: $wm;
  padding: 7px 0 0 0;
  cursor: pointer;
}

.legal-link {
  display: inline-block;
  color: $wl;
  border-bottom: 1px solid $wl;
  line-height: 13px;
}
</style>

