<template>
  <div id="pre-register">
    <div class="contents-limiter">
      <div class="columns">
        <div class="column plus-block is-6 is-offset-3">
          <h4 class="h4">
            Be the first who gets
            <span class="plus">PLUS</span>.
          </h4>
          <p class="subtitle-1">
            PLUS plan is not available right now. Register to let you know when
            it’s become available.
          </p>

          <div class="register" @keyup.enter="subscribe">
            <input
              v-model="email"
              type="email"
              id="EMAIL"
              name="EMAIL"
              autocomplete="off"
              class="body-2"
              placeholder="Email address"
              data-required="true"
              required
            />
            <div class="button-block">
              <input
                form="sib-form"
                type="button"
                @click="subscribe"
                class="button-2"
                :class="{ loading: isAnimate, done: isDone }"
                :disabled="isAnimate || isDone"
                value="pre-register"
              />
              <span :class="{ spiner: isAnimate }"></span>

              <img
                src="../assets/done_24px.png"
                srcset="../assets/done_24px.png 1x, ../assets/done_24px_2x.png 2x"
                class="done-icon"
                v-show="isDone"
                alt="Done Icon"
              />
            </div>
            <p class="error">{{ errors }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "pre-register",
  data() {
    return {
      email: "",
      errors: "",
      clickTime: 0,
      currentTime: 0,
      isAnimate: false,
      isDone: false
    };
  },
  methods: {
    validEmail(email) {
      var regex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return regex.test(email);
    },

    subscribe() {
      if (!this.email) {
        this.errors = "Email required.";

        this.$ga.event({
          eventCategory: "submiter",
          eventAction: "empty_email_error",
          eventLabel: "plus_pre_register",
          eventValue: 0
        });
      } else if (!this.validEmail(this.email)) {
        this.errors = "Invalid email address.";

        this.$ga.event({
          eventCategory: "submiter",
          eventAction: "email_validation_error",
          eventLabel: "plus_pre_register",
          eventValue: 0
        });
      } else {
        this.clickTime = new Date().getTime();
        this.isAnimate = true;
        let formData = {
          EMAIL: this.email,
          email_address_check: "",
          locale: "en",
          html_type: "simple"
        };
        const encodeForm = data => {
          return Object.keys(data)
            .map(
              key =>
                encodeURIComponent(key) + "=" + encodeURIComponent(data[key])
            )
            .join("&");
        };
        axios
          .post(
            "https://sibforms.com/serve/MUIEAMN6HJ-KkUTr5QxpucOj-pb_Cf-Tfp1TlshK2y7AJT52ZQvPOb7R9VdibDVNWjkNR5kP7loWe40HtYl9bPqHjyqZlR7_SWB8GkSh7VLVlXyFV0ZNjTKwNDAN3g2w2jD2RrZbdRv3VDYQRFxqXxa9mBzwmm5uhi2KbXFsJK-1pG_LZnsyB6peMe_JzRs58fUPrHSRmQDwtANU",
            encodeForm(formData)
          )
          .then(() => {
            this.currentTime = new Date().getTime();
            const timeDiffrence = this.currentTime - this.clickTime;

            if (timeDiffrence > 1000) {
              setTimeout(() => {
                this.isAnimate = false;
                this.isDone = true;
              }, timeDiffrence);
            } else {
              this.isAnimate = true;
              setTimeout(() => {
                this.isAnimate = false;
                this.isDone = true;
              }, 1000);
            }

            this.$ga.event({
              eventCategory: "submiter",
              eventAction: "email_registering",
              eventLabel: "plus_pre_register",
              eventValue: 0
            });
          })
          .catch(reason => {
            this.currentTime = new Date().getTime();
            const timeDiffrence = this.currentTime - this.clickTime;

            if (timeDiffrence > 1000) {
              setTimeout(() => {
                this.isAnimate = false;
                this.isDone = true;
              }, timeDiffrence);
            } else {
              this.isAnimate = true;
              setTimeout(() => {
                this.isAnimate = false;
                this.isDone = true;
              }, 1000);
            }

            this.$ga.event({
              eventCategory: "submiter",
              eventAction: "email_registering",
              eventLabel: "plus_pre_register",
              eventValue: 0
            });
          });
      }
    }
  },

  watch: {
    email: function(newVal, oldVal) {
      if (this.email && this.errors === "Email required.") {
        this.errors = "";
      } else if (
        (!this.email && this.errors === "Invalid email address.") ||
        this.validEmail(this.email)
      ) {
        this.errors = "";
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@include override-gaps;
#pre-register {
  height: calc(100vh - 100px);
  min-height: 721px;
}

.plus-block {
  padding: 208px 0 232px 32px;
  @include until($tablet) {
    padding: 168px 1.5rem 184px;
  }
}

.h4 {
  margin: 0 0 129px 0;
  color: $wh;
  @include until($tablet) {
    margin: 0 0 120px 0;
  }

  .plus {
    color: $s1;
  }
}

.subtitle-1 {
  color: $wh;
  margin: 0 18% 21px 0;
}

.register {
  display: flex;
  flex-wrap: wrap;
  width: 80%;
  max-width: 384px;
  @include until($tablet) {
    width: 100%;
  }
}

.body-2 {
  max-width: calc(100% - 141px);
  padding: 0 16px;
  background-color: $bh;
  border: 1px solid $bl;
  color: $wh;
  outline: none;
}

.button-2 {
  padding: 14px 20px 14px 20px;
  background-color: $s1;
  color: $bh;
  border: none;
  outline: none;
  transition: background-color 150ms cubic-bezier(0.645, 0.045, 0.355, 1);

  &:hover {
    background-color: $hover2;
  }

  &:active {
    background-color: $press2;
  }

  @include until($tablet) {
    padding: 14px 15px 14px 15px;
  }
}

.button-block {
  position: relative;
}

.loading {
  color: transparent;
  background-color: $loading;

  &:hover {
    background-color: $loading;
  }
}

.spiner {
  border: 2px solid transparent;
  border-top-color: $bh;
  border-right-color: $bh;
  border-radius: 50%;
  width: 1.5em;
  height: 1.5em;
  animation: spin 1s linear infinite;
  position: absolute;
  top: calc(50% - 0.75rem);
  left: calc(50% - 0.75rem);
  z-index: 2;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.done {
  color: transparent;
  background-color: $hover2;

  &:hover {
    background-color: $hover2;
  }
}

.done-icon {
  position: absolute;
  top: 7px;
  left: 58px;
  z-index: 2;
  filter: invert(100%);
}

.error {
  color: $error;
  margin: 7px 0 0;
  flex-basis: 100%;
}
</style>
