<template>
  <div id="uploader" class="columns is-centered" ref="fileform">
    <input type="text" class="copyInput" ref="copyInput"/>
    <div class="column is-6-desktop is-12-tablet is-12-mobile is-paddingless">
      <div class="container">
        <form
            class="form"
            :class="{'formhover': isHover}"
            @mouseenter="handleFormMouseEnter"
            @mouseleave="handleFormMouseLeave"
        >
          <input
              type="file"
              id="directory"
              v-show="!isUpload"
              ref="directory"
              webkitdirectory
              mozdirectory
              directory
              multiple
              v-on:change="handleDirectoryUpload()"
          />

          <input
              type="file"
              id="file"
              v-show="!isUpload"
              ref="file"
              multiple
              v-on:change="handleFileUpload()"
          />

          <div class="media">
            <!-- Thumbnail -->
            <div class="media-left">
              <div class="add-file" v-show="!isUpload">
                <img
                    src="../assets/i_add_wm_24px.png"
                    alt="Plus Icon"
                    srcset="../assets/i_add_wm_24px.png 1x, ../assets/i_add_wm_24px_2x.png 2x"
                    class="add-icon"
                />
              </div>
              <figure v-show="isUpload">
                <img
                    src="../assets/i_file_icon.png"
                    srcset="../assets/i_file_icon.png 1x, ../assets/i_file_icon_2x.png 2x"
                    alt="File Icon"
                />
                <p class="file-format">{{ fileFormat.substr(0, 4) }}</p>
              </figure>
            </div>

            <!-- Progressbar and Info -->
            <div class="media-content">
              <p class="upload-text body-2" v-show="!isUpload">{{ uploadTextContent }}</p>
              <div class="upload-content" v-show="isUpload">
                <p class="filename body-2">{{ fileName }}</p>
                <p class="upload-info body-3">{{ isLinkedCopied ? 'Link copied.' : uploadInfo }}</p>
                <div class="progressbar" v-show="beShown">
                  <span
                      class="download bar"
                      :class="{ 'is-stuck': isStuck }"
                      :style="{ width: uploadPercent + '%' }"
                  ></span>
                  <span class="process bar" :style="{ width: processPercent + '%' }"></span>
                </div>
              </div>
            </div>
            <!-- Functional button -->
            <div class="media-right tooltips">
              <!-- More button -->
              <a
                  href="#"
                  class="buttons more"
                  ref="menuPanel"
                  @click="handleMoreButtonClick($event)"
              >
                <img
                    class="logo"
                    src="../assets/more_hor_24px.png"
                    srcset="../assets/more_hor_24px.png, ../assets/more_hor_24px_2x.png 2x"
                    alt="More Button"
                />
                <MenuPanel v-show="isMoreButtonClicked">
                  <button
                      v-show="!isUpload"
                      class="menu-item link-2"
                      @click="openFileDialog($event)"
                  >Upload File
                  </button>
                  <button
                      v-show="!isUpload"
                      class="menu-item link-2"
                      @click="openDirectoryDialog($event)"
                  >Upload Folder
                  </button>
                  <button
                      v-show="buttonStatus === 'cancel' && isUpload"
                      :disabled="processing"
                      class="menu-item link-2"
                      @click="btnCancelUpload"
                  >Cancel
                  </button>
                  <button
                      v-show="isUpload"
                      :disabled="!uploader || !uploader.url"
                      class="menu-item link-2"
                      @click="copyUploaderLink($event)"
                  >Copy link
                  </button>
                </MenuPanel>
              </a>
              <!-- Cancel button -->
              <a
                  href="#"
                  @click="btnCancelUpload"
                  class="buttons cancel"
                  :disabled="processing"
                  v-show="buttonStatus === 'cancel' && isUpload"
              >
                <img
                    src="../assets/i_close_24px.png"
                    srcset="../assets/i_close_24px.png 1x, ../assets/i_close_24px_2x.png 2x"
                    alt="Forward button"
                />
              </a>
            </div>
          </div>
        </form>
        <div id="list" v-show="uploadList.length !== 0">
          <UploaderListItem
              v-for="(listItem, index) in uploadList"
              :key="listItem.H.KY"
              ref="listItem"
              :disabled="listItem.Disabled"
              :copy-disabled="index > 2 || itemIndex === index"
              :idx="index"
              :index="listItem.H.KY"
              :listItem="listItem"
              :showTooltip="showTooltip == listItem.H.KY"
              :requireAPassword="requireAPassword == listItem.H.KY"
              :needsToRename="needsToRename == listItem.H.KY"
              :isMoreClicked="isMoreClicked == listItem.H.KY"
              :isItemHover="isItemHover === listItem.H.KY"
              :isMobileViewport="mobileViewport"
              :copyCaption="copyCaption === listItem.H.KY"
              :captionOnCopyHover="captionOnCopyHover === listItem.H.KY"
              @rename="needsToRename = listItem.H.KY"
              @password="handlePassword"
              @password-cancel="requireAPassword = -1"
              @rename-cancel="needsToRename = -1"
              @password-done="handlePasswordDone"
              @rename-done="handleRename"
              @more-clicked="isMoreClicked = listItem.H.KY"
              @more-clicked-cancel="isMoreClicked = -1;"
              @hover="handleHoverItems"
              @hover-cancel="handleCancelHover"
              @copy-link="handleCopyLink($event)"
              @delete="handleDelete($event)"
              @mouseenter="handleMouseEnter"
              @mouseleave="handleMouseLeave"
          ></UploaderListItem>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BHUploader from "../uploader/index";
import {userSession, storage} from "../user";
import clipboard from "clipboard";
import {setTimeout, clearTimeout} from "timers";
import UploaderListItem from "../components/UploaderListItem";
import MenuPanel from "../components/MenuPanel";
import axios from "axios";

export default {
  name: "upload",
  components: {
    UploaderListItem,
    MenuPanel
  },

  data() {
    return {
      // isDisabled: false,
      fileFormat: "...",
      fileName: "Getting file information...",
      fileSize: 0,
      uploadInfo: "Loading...",
      speedInfo: "Loading...",
      uploadPercent: 0,
      processPercent: 0,
      lastPercent: 0,
      isHover: false,

      buttonStatus: "cancel",
      beShown: true,
      isStuck: false,

      isUpload: false,
      // Confirm that browser is drag and drop capable
      dragAndDropCapable: false,
      uploader: null,

      showTooltip: -1,
      processing: false,
      tooltipOnHover: null,

      uploadList: [],

      isMoreButtonClicked: false,
      isMoreClicked: -1,
      isItemHover: -1,
      requireAPassword: -1,
      needsToRename: -1,
      copyCaption: -1,
      captionOnCopyHover: -1,
      isLinkedCopied: false,
      itemIndex: -1,

      mobileViewport: false,
    };
  },
  mounted() {
    this.renewUploader();
    axios.post("https://police.blackhole.run/api/b", {
      b: "web"
    });
    // userSession.listFiles((name) => userSession.deleteFile(name));
    if (localStorage.getItem("first_copy") == "true") {
      this.$emit("display-banner", 0);
    }

    this.dragAndDropCapable = this.determineDragAndDropCapable();

    if (this.dragAndDropCapable && !this.isUpload) {
      const dragEvents = ["drag", "dragstart", "dragend", "dragover", "drop"];
      dragEvents.forEach(
          function (event) {
            this.$refs.fileform.addEventListener(
                event,
                function (e) {
                  e.preventDefault();
                  e.stopPropagation();
                }.bind(this),
                false
            );
          }.bind(this)
      );

      this.$refs.fileform.addEventListener(
          "dragenter",
          function () {
            this.isHover = true;
          }.bind(this)
      );
      this.$refs.fileform.addEventListener(
          "dragleave",
          function () {
            this.isHover = false;
          }.bind(this)
      );
      this.$refs.fileform.addEventListener(
          "drop",
          function (e) {
            if (this.isUpload) return;
            this.fileName = "Getting file information...";
            this.uploader.processDragedFiles(e.dataTransfer, result => {
              if (result.done) {
                if (result.error != "") {
                  // console.log(result.error);
                  this.fileName = result.fileName;
                  this.isUpload = true;
                  this.beShown = false;
                  this.uploadInfo = "File size limited to 512 MB.";
                  this.buttonStatus = "cancel";
                  this.processing = false;
                  return;
                } else {
                  // this.uploader.setPassword("123");
                  this.uploader.canceled = false;
                  this.uploader.upload(result.fileName);
                }
              }
            });
          }.bind(this)
      );
    }

    let cb = new clipboard("#copyToClipboard", {
      text: () => {
        return this.uploader.url;
      }
    });

    cb.on("success", () => {
      localStorage.setItem("first_copy", "true");
      // if (localStorage.getItem("tooltip") == undefined) {
      localStorage.setItem("tooltip", "5");
      // } else {
      //   localStorage.setItem(
      //     "tooltip",
      //     Number.parseInt(localStorage.getItem("tooltip")) + 1
      //   );
      // }
      this.$emit("display-banner", 5000);
      // this.isDisabled = true;
      this.uploadInfo = "Link copied, you can share it with anyone.";
      this.showTooltip = -1;
      setTimeout(() => {
        // this.isDisabled = false;
        this.uploadInfo = "Upload complete.";
      }, 3000);
    });

    this.loadUserFile();
    this.getMediaQueries();
  },

  methods: {
    renewUploader() {
      this.uploader = new BHUploader(
          userSession.loadUserData().hubUrl,
          userSession.loadUserData().appPrivateKey
      );
      this.uploader.onStart = (fileName, fileSize) => {
        this.isUpload = true;
        this.uploadPercent = 0;
        this.fileName = fileName;
        this.fileSize = fileSize;
        let fx = fileName.substr(fileName.lastIndexOf("."));
        if (fx[0] == ".") fx = fx.substr(1);
        this.fileFormat = fx;
        this.uploadInfo = "Uploading 0 of " + this.byteToNormalString(fileSize);
        this.uploadPercent = 0;
      };

      this.uploader.onProgress = (loaded, total, processing) => {
        this.processing = processing;
        if (processing) {
          // console.log("process")
          this.isUpload = true;
          this.uploadPercent = 0;
          this.processPercent = Math.min(
              Math.floor((loaded * 100) / total),
              100
          );
          this.uploadInfo = "Processing...";
        } else {
          this.processPercent = 100;
          loaded = Math.min(loaded, total);
          this.uploadInfo =
              "Uploading " +
              this.byteToNormalString(loaded) +
              " of " +
              this.byteToNormalString(total);
          this.uploadPercent = Math.min(
              Math.floor((loaded * 100) / total),
              100
          );
        }
      };

      this.uploader.onCancel = () => {
        this.isUpload = false;
        this.renewUploader();
      };

      this.uploader.onFinish = (url, item) => {
        // this.buttonStatus = "copy";
        // this.uploadInfo = "Upload complete.";
        // this.beShown = false;
        this.uploadList.unshift(item);
        this.isUpload = false;
        this.showTooltip =
            localStorage.getItem("tooltip") == undefined ||
            Number.parseInt(localStorage.getItem("tooltip")) < 2
                ? 0
                : -1;
      };

      this.uploader.onRename = HFN => {
        this.uploadList.forEach((h, idx) => {
          if (HFN == h.HFN) {
            h.Disabled = false;
            this.uploadList[idx] = h;
          }
        });
        this.$forceUpdate();
      };

      this.uploader.onPassword = HFN => {
        this.uploadList.forEach((h, idx) => {
          if (HFN == h.HFN) {
            h.Disabled = false;
            this.uploadList[idx] = h;
          }
        });
        this.$forceUpdate();
      };

      this.uploader.onDelete = HFN => {
        this.uploadList = this.uploadList.filter(h => {
          return HFN != h.HFN;
        });
        this.$forceUpdate();
      };
    },
    handlePassword(e) {
      let index = this.uploadList.findIndex((item) => {
        return item.H.KY == e.index;
      });
      if (this.uploadList[index].H.PAS != "") {
        this.handlePasswordDone({index: e.index, password: ""});
      } else {
        this.requireAPassword = e.index;
      }
    },
    handlePasswordDone(e) {
      let index = this.uploadList.findIndex((item) => {
        return item.H.KY == e.index;
      });
      this.isMoreClicked = -1;
      this.isMoreButtonClicked = false;
      this.requireAPassword = -1;
      this.uploadList[index].H.PAS = e.password;
      this.uploadList[index].Disabled = true;
      this.uploader.changePassword(
          this.uploadList[index].HFN,
          this.uploadList[index].H,
          e.password
      );
    },
    async handleRename(e) {
      this.isMoreClicked = -1;
      this.isMoreButtonClicked = false;
      let index = this.uploadList.findIndex((item) => {
        return item.H.KY == e.index;
      });
      this.uploadList[index].H.FN = e.name;
      this.uploadList[index].Disabled = true;
      this.needsToRename = -1;

      await this.uploader.renameFile(
          this.uploadList[index].HFN,
          this.uploadList[index].H,
          e.name
      );
    },
    handleCopyLink(e) {
      let index = this.uploadList.findIndex(item => {
        return item.H.KY == e.index;
      })
      if (index >= 3) return;
      localStorage.setItem("tooltip", "5");
      let item = this.uploadList[index];
      let url = this.uploader.getURL(item.H);
      this.$refs["copyInput"].value = url;
      this.$refs["copyInput"].style = "visibility: visible";
      this.$refs["copyInput"].select();
      document.execCommand("copy");
      this.$refs["copyInput"].style = "visibility: hidden";
      // To Remove Caption on hover and copy tooltip
      this.captionOnCopyHover = -1;
      this.showTooltip = -1;
      this.itemIndex = index;
      // To Show Link copied. caption for 3 seconds
      this.copyCaption = e.index;
      clearTimeout(removeCaption);
      const removeCaption = setTimeout(() => {
        this.copyCaption = -1;
        this.itemIndex = -1;
      }, 3000);
    },
    copyUploaderLink(event) {
      event.stopPropagation();
      event.preventDefault();
      let url = this.uploader.url;
      this.$refs["copyInput"].value = url;
      this.$refs["copyInput"].style = "visibility: visible";
      this.$refs["copyInput"].select();
      document.execCommand("copy");
      this.$refs["copyInput"].style = "visibility: hidden";
      // To Show Link copied. caption for 3 seconds and close menu panel
      this.isLinkedCopied = true;
      this.isMoreButtonClicked = false;
      clearTimeout(removeCopyCaption);
      const removeCopyCaption = setTimeout(() => {
        this.isLinkedCopied = false;
      }, 3000);
    },
    handleDelete(e) {
      this.isMoreClicked = -1;
      this.isMoreButtonClicked = false;
      let index = this.uploadList.findIndex((item) => {
        return item.H.KY == e.index;
      });
      let item = this.uploadList[index];
      this.uploadList[index].Disabled = true;
      this.uploader.deleteFile(item.HFN, item.H).then(() => {
        return storage.deleteFile(item.HFN);
      });
    },
    loadUserFile() {
      storage.listFiles(name => {
        storage.getFile(name, {decrypt: true}).then(async e => {
          if (e) {
            let H = JSON.parse(e);
            if (H.BHV == 7) {
              H = await this.uploader.upgradeHistory(name, H);
            }
            let item = {
              HFN: name,
              H
            };
            this.uploadList.push(item);
            this.uploadList.sort((a, b) => {
              if (a.H.TS > b.H.TS) return -1;
              if (a.H.TS < b.H.TS) return 1;
              if ((a.H.TS = b.H.TS)) return 0;
            });
          }
        });
        return true;
      });
    },
    btnCancelUpload() {
      if (this.processing) return;
      this.uploader.canceled = true;
      this.isUpload = false;
      this.renewUploader();
      // this.fileName = "Canceling ...";
      // this.uploadInfo = "";
      // this.processing = true;
    },

    onMouseEnter() {
      // if (!this.isDisabled) {
      this.uploadInfo = "Copy share link to clipboard.";
      this.tooltipOnHover = setTimeout(() => {
        this.showTooltip = -1;
      }, 500);
      // }
    },

    onMouseLeave() {
      // if (!this.isDisabled) {
      this.uploadInfo = "Upload complete.";
      if (this.showTooltip) {
        this.showTooltip = -1;
      }
      clearTimeout(this.tooltipOnHover);
      this.tooltipOnHover = null;
      // }
    },

    handleMouseEnter(index) {
      let sttIndex = this.uploadList.findIndex(item => {
        return item.H.KY == index;
      })

      this.tooltipOnHover = setTimeout(() => {
        this.showTooltip = index;
      }, 650);
      if (sttIndex < 3) {
        this.captionOnCopyHover = index;
      }
    },

    handleMouseLeave() {
      clearTimeout(this.tooltipOnHover);
      this.tooltipOnHover = null;
      this.showTooltip = -1;
      this.captionOnCopyHover = -1;
    },

    byteToNormalString(input) {
      if (input + 100 * 1024 * 1024 > 1024 * 1024 * 1024) {
        input = input / (1024 * 1024 * 1024);
        return input.toFixed(1) + " GB";
      } else if (input + 100 * 1024 > 1024 * 1024) {
        input = input / (1024 * 1024);
        return input.toFixed(1) + " MB";
      } else if (input + 100 > 1024) {
        return (input / 1024).toFixed(1) + " KB";
      } else {
        return input + " B";
      }
    },

    // Determines if the drag and drop functionality is in the window
    determineDragAndDropCapable() {
      // Create a test element to see if certain events are present that let us do drag and drop.
      var div = document.createElement("div");
      return (
          ("draggable" in div || ("ondragstart" in div && "ondrop" in div)) &&
          "FormData" in window &&
          "FileReader" in window
      );
    },

    handleFileUpload() {
      if (this.isUpload) return;
      this.fileName = "Getting file information...";
      this.uploader.processDragedFiles(this.$refs.file, result => {
        if (result.done) {
          if (result.error != "") {
            // console.log(result.error);
            this.fileName = result.fileName;
            this.isUpload = true;
            this.beShown = false;
            this.uploadInfo = "File size limited to 512 MB.";
            this.buttonStatus = "cancel";
            this.processing = false;
            return;
          } else {
            // this.uploader.setPassword("123");
            this.uploader.canceled = false;
            this.uploader.upload(result.fileName);
          }
        }
      });
    },

    handleDirectoryUpload() {
      if (this.isUpload) return;
      this.fileName = "Getting file information...";
      this.uploader.processDragedFiles(this.$refs.directory, result => {
        if (result.done) {
          if (result.error != "") {
            // console.log(result.error);
            this.fileName = result.fileName;
            this.isUpload = true;
            this.beShown = false;
            this.uploadInfo = "File size limited to 512 MB.";
            this.buttonStatus = "cancel";
            this.processing = false;
            return;
          } else {
            // this.uploader.setPassword("123");
            this.uploader.canceled = false;
            this.uploader.upload(result.fileName);
          }
        }
      });
    },

    openFileDialog(event) {
      this.isMoreButtonClicked = false;
      event.stopPropagation();
      event.preventDefault();
      this.$refs.file.click();
    },

    openDirectoryDialog(event) {
      this.isMoreButtonClicked = false;
      event.stopPropagation();
      event.preventDefault();
      this.$refs.directory.click();
    },

    handleFormMouseLeave() {
      if (this.isMoreButtonClicked) return;
      this.isHover = false;
    },

    handleFormMouseEnter() {
      this.isHover = true;
      this.isMoreClicked = -1;
      this.isItemHover = -1;
    },

    handleMoreButtonClick(event) {
      event.stopPropagation();
      event.preventDefault();
      this.isMoreButtonClicked = true;
    },

    handleFormContextMenu() {
      if (this.isMoreButtonClicked) return;
      this.isHover = false;
    },

    handleDocumentClick(event) {
      if (this.isMoreButtonClicked) {
        const menuPanel = this.$refs.menuPanel.$el;
        let target = event.target;
        if (menuPanel !== target || !menuPanel.contains(target)) {
          this.isMoreButtonClicked = false;
          this.isHover = false;
        }
      }

      if (this.isMoreClicked !== -1 && this.isItemHover !== -1) {
        let moreIndex = this.uploadList.findIndex((item) => {
          return item.H.KY == this.isMoreClicked;
        })
        const listItemMenu = this.$refs.listItem[moreIndex].$children[0].$el;
        let target = event.target;
        if (listItemMenu !== target && !listItemMenu.contains(target)) {
          this.isMoreClicked = -1;
          let hoverIndex = this.uploadList.findIndex((item) => {
            return item.H.KY == this.isItemHover;
          })
          const listItem = this.$refs.listItem[hoverIndex].$el;
          if (listItem !== target && !listItem.contains(target)) {
            this.isItemHover = -1;
          }
        }
      }

      if (this.requireAPassword !== -1) {
        let index = this.$refs.listItem.findIndex((item) => {
          return item.index == this.requireAPassword;
        })
        const listItem = this.$refs.listItem[index].$el;
        let target = event.target;
        if (listItem !== target && !listItem.contains(target)) {
          this.requireAPassword = -1;
        }
      }

      if (this.needsToRename !== -1) {
        let ntrIndex = this.uploadList.findIndex((item) => {
          return item.H.KY == this.needsToRename;
        })
        const listItem = this.$refs.listItem[ntrIndex].$el;
        let target = event.target;
        if (listItem !== target && !listItem.contains(target)) {
          this.needsToRename = -1;
        }
      }
    },

    handleEscClicked(event) {
      if (event.keyCode === 27) {
        this.isMoreClicked = -1;
        this.needsToRename = -1;
        this.requireAPassword = -1;
        this.isMoreButtonClicked = false;
      }
    },

    handleHoverItems(index) {
      this.isHover = false;
      this.isMoreButtonClicked = false;
      this.isItemHover = index;
    },

    handleCancelHover() {
      this.isItemHover = -1;
      this.isMoreClicked = -1;
    },

    getMediaQueries() {
      let mobileViewport = window.matchMedia("(max-width: 1023px)");
      this.mobileViewport = mobileViewport.matches;
    }
  },

  computed: {
    uploadTextContent() {
      if (this.mobileViewport) {
        return 'Add your file to upload.'
      } else {
        return 'Add your file to start uploading.'
      }
    }
  },

  created() {
    window.addEventListener("resize", this.getMediaQueries);
    document.addEventListener("click", this.handleDocumentClick);
    document.addEventListener("keydown", this.handleEscClicked);
  },
  destroyed() {
    window.removeEventListener("resize", this.getMediaQueries);
    // important to clean up!!
    document.removeEventListener("click", this.handleDocumentClick);
    document.removeEventListener("keydown", this.handleEscClicked);
  }
};
</script>

<style lang="scss" scoped>
@include override-gaps;

.container {
  padding: 8px 0;
  border: 1px solid $bm;
  background-color: $bh;
  box-sizing: border-box;
  box-shadow: 0px 20px 40px rgba(0, 0, 0, 0.16),
  0px 10px 20px rgba(0, 0, 0, 0.24);

  margin: 0 auto;
  max-width: 480px;
  min-width: 320px;

  @include until(479px) {
    margin: 0 0 32px;
  }
}

.media {
  max-width: 480px;
  min-width: 360px;
  height: 88px;
  border: none !important;
  margin: 0 !important;
  padding: 0 !important;

  @include until($tablet) {
    min-width: 360px;
    height: 80px;
  }

  @include until(375px) {
    min-width: 320px;
    height: 80px;
  }
}

.media-left {
  height: 100%;
  margin: 0;
  display: flex;

  @include until(360px) {
    display: none;
  }

  figure {
    position: relative;
    margin: auto 0 auto 29px;

    @include until(375px) {
      margin: auto 0 auto 17px;
    }

    @include until(360px) {
      display: none;
    }

    img {
      display: block;
      width: 38px;
      height: 50px;
    }
  }

  .file-format {
    position: absolute;
    top: 29px;
    left: 50%;
    transform: translate(-50%, 0);
    color: $wm;
  }

  .add-file {
    width: 44px;
    height: 44px;
    display: flex;
    border: 1px solid $bl;
    border-radius: 5px;
    margin: auto 0 auto 30px;

    @include until(375px) {
      margin: auto 0 auto 14px;
    }

    .add-icon {
      margin: auto;
      opacity: 0.38;

      img {
        width: 24px;
        height: 24px;
      }
    }
  }

  .add-hover {
    background-color: $bl;
  }
}

.media-content {
  height: 100%;
  margin: 0;
  display: flex;

  .upload-content {
    margin: 0 0 0 17px;
    padding: 16px 0 24px 0;
    min-width: 0;
    flex: 1;
    max-width: 262px;

    @include until($tablet) {
      margin: 0 0 0 17px;
      padding: 12px 0 16px 0;
    }

    @include until(375px) {
      margin: 0 0 0 17px;
      padding: 12px 0 16px 0;
    }

    @include until(360px) {
      margin: 0 0 0 22px;
      padding: 12px 0 16px 0;
    }
  }

  .filename {
    color: $wh;
    margin: 0 0 2px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .upload-info {
    color: $wm;
    margin: 0 0 8px 0;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .upload-text {
    margin: auto 0 auto 17px;
    color: $wm;

    @include until($tablet) {
      margin: auto 0 auto 14px;
    }

    @include until(375px) {
      margin: auto 0 auto 22px;
    }
  }

  .text-hover {
    color: $wh;
  }
}

.progressbar {
  position: relative;
  height: 4px;
  width: 100%;
  background-color: $bm;

  .bar {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    transition: width 150ms ease-in-out;
  }

  .download {
    background-color: $p1;
    z-index: 2;
  }

  .process {
    background-color: $bl;
    z-index: 1;
  }
}

.media-right {
  margin: auto 24px auto 24px;
  width: auto;
  height: 32px;
  display: flex;

  @include until(375px) {
    margin: auto 16px auto 24px;
  }

  @include until(360px) {
    margin: auto 18px auto 24px;
  }

  @include from($tablet) {
    margin: auto 28px auto 24px;
  }

  .buttons[disabled] {
    background-color: transparent;

    img {
      opacity: 0.2;
    }

    &:hover {
      background-color: transparent;

      img {
        opacity: 0.2;
      }
    }

    &:active {
      background-color: transparent;
    }
  }

  .buttons {
    border-radius: 5px;
    width: 32px;
    height: 32px;
    padding: 0;
    background-color: transparent;
    border: none;
    outline: none;
    transition: background-color 150ms ease-in-out;
    display: flex;

    img {
      margin: auto;
      width: 24px;
      height: 24px;
      opacity: 0.5;
      transition: opacity 150ms ease-in-out;
    }

    &:hover {
      background-color: $bm;

      img {
        opacity: 1;
      }
    }

    &:active {
      background-color: $bl;
    }

    &.cancel {
      margin: 0 0 0 16px;
    }

    &.more {
      display: none;
      @include touch {
        display: flex;
        z-index: 2;
      }
    }
  }
}

.is-stuck {
  opacity: 0.38;
}

.form {
  position: relative;

  &.formhover {
    .media {
      background-color: $wh-ov04;
      @include touch {
        background-color: transparent;
      }
    }

    .upload-text {
      color: $wh;
    }

    .add-file {
      background-color: $wh-ov12;

      .add-icon {
        opacity: 1;
      }
    }

    .more {
      display: flex;
      z-index: 2;
    }
  }

  #file,
  #directory {
    max-width: 480px;
    min-width: 320px;
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    outline: none;

    &::-webkit-file-upload-button {
      visibility: hidden;
    }
  }
}

input[type="file"] {
  opacity: 0;
}

.media-hover {
  background-color: $wh-ov04;
}

.copyInput {
  visibility: hidden;
  position: absolute;
  left: -99999;
  top: -99999;
}
</style>
