<template>
  <div id="redirect">
    <div class="background-color">
      <div class="radial-gradient">
        <div class="dithering"></div>
      </div>
    </div>
    <div class="contents-limiter">
      <div class="app-block columns is-marginless is-multiline is-centered">
        <!-- Navigation bar -->
        <div class="app-navbar column is-paddingless is-12 is-flex">
          <router-link to="/">
            <img
              class="logo"
              src="../assets/‌logo_24_wm.png"
              srcset="../assets/‌logo_24_wm.png    1x, ../assets/‌logo_24_wm_2x.png 2x"
              alt="BlackHole Logo"
            />
          </router-link>
        </div>
        <div class="column redirect-note is-paddingless is-6" style="text-align:center">
          <p class="body-1" v-html="text"></p>
          <a ref="continueLink" href="#" class="link-1">{{ continueText }}</a>
          <span v-html="hiddenContainer"></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import pako from "pako";
import base64url from "base64url";

export default {
  name: "redirect",
  data() {
    return {
      text: "",
      continueText: "",
      hiddenContainer: ""
    };
  },
  
  mounted() {
    if (window.location.search.startsWith("?authRequest")) {
      this.text =
        "Please wait to redirect to blockstack or click on continue to blockstack";
      this.continueText = "Continue to blockstack";
      this.$refs.continueLink.href =
        "https://browser.blockstack.org/auth" + window.location.search;
      window.open(
        "https://browser.blockstack.org/auth" + window.location.search,
        "_self"
      );
    } else if (window.location.search.startsWith("?authResponse")) {
      let param = window.location.search;
      param = param.substr(param.indexOf("?"));
      let params = param.split(".");
      this.text = "You are now signed in. Please allow BlackHole to <br/> continue to the app.";
      this.continueText = "Continue to app";
      param =
        params[0] +
        "." +
        base64url(pako.deflateRaw(base64url.decode(params[1]))) +
        "." +
        params[2];

      let url = "blackhole://signin/" + param;
      this.$refs.continueLink.href = url;
      this.hiddenContainer = `<div style="display:none"><iframe src="${url}"></iframe></div>`;
    } else {
      this.text = "You are now signed in. Please allow BlackHole to <br/> continue to the app.";
      this.continueText = "Continue to app";
      let url = "blackhole://signin/" + window.location.hash.substr(1);
      console.log(url);
      this.$refs.continueLink.href = url;
      this.hiddenContainer = `<div style="display:none"><iframe src="${url}"></iframe></div>`;
    }
  }
};
</script>

<style lang="scss">
@include override-gaps;
#redirect {
  position: relative;
  overflow: hidden;
}

.background-color {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: $bh;
    z-index: -1;

    .radial-gradient {
      position: absolute;
      width: 2244px;
      height: 2244px;
      left: 368px;
      top: 244px;
      @include until($tablet) {
        left: -127px;
        top: 44px;
      }

      background: radial-gradient(
        1122px at 50% 50%,
        $p2 0%,
        $p3 37.5%,
        $p4 70.83%,
        $bh 100%
      );
      filter: opacity(0.25);

      .dithering {
        position: absolute;
        background-image: radial-gradient(
            1122px at 50% 50%,
            transparent 90%,
            $bh 100%
          ),
          url("../assets/noisy_texture_150px.png");
        width: 100%;
        height: 100%;
      }
    }
  }

.app-block {
  display: flex;
  height: 100vh;
  min-height: 568px;

  @include until($tablet) {
    // to override bulma responsiveness in mobile view
    display: flex;
    & > .column {
      flex: 100%;
    }
  }
}

.app-navbar {
  display: flex;
  align-items: center;
  align-self: flex-start;
  height: 80px;
  padding: 0 32px;

  @include until($tablet) {
    height: 64px;
    padding: 0 24px;
    // to override bulma responsiveness in horizental mobile view
  }

  .logo {
    width: 132px;
    height: 24px;
    margin: 0;
  }

  .router-link-active{
    display: block;
    height: 24px;
    width: auto;
  }
}

.redirect-note {
  margin: -80px 32px 0 32px;
  @include until($tablet) {
    margin: -64px 0 0 0;
  }
  .body-1 {
    color: $wm;
    text-align: center;
    margin: 0 0 34px;
  }

  .link-1{
    color: $wm;
    border-bottom: 1px solid $wm;
  }
}
</style>
