<template>
  <div class="base-input">
    <label class="caption" :for="name" v-if="label">{{ label }}</label>
    <input
      class="input body-2"
      :type="type"
      :name="name"
      :id="name"
      :placeholder="placeholder"
      :disabled="isDisabled"
      :value="value"
      @input="onInput($event.target.value)"
      @focus="onFocus($event.target.value)"
      @blur="onBlur($event.target.value)"
    />
    <p class="error" v-show="error">{{ error }}</p>
  </div>
</template>

<script>
export default {
  name: "BaseInput",
  props: {
    label: String,
    name: String,
    type: { type: String, default: "text" },
    placeholder: String,
    isDisabled: Boolean,
    error: String,
    value: String
  },

  methods: {
    onInput(value) {
      this.$emit("input", { value: value });
    },

    onFocus(value) {
      this.$emit("focus", { value: value });
    },

   onBlur(value) {
      this.$emit("blur", { value: value });
    },
  },
};
</script>

<style lang="scss" scoped>
.base-input {
  .input {
    border: none;
    outline: none;
    padding: 10px 12px;
    background-color: $bh;
    box-shadow: 0 0 0 1px transparent, 0 0 0 1px $wh-ov24 inset;
    color: $wh;
    caret-color: $p1;
    width: 100%;
    // min-width: 0;
    transition: all 50ms ease-in-out;

    display: flex;
    flex-direction: column;

    &::placeholder {
      color: $wl;
    }

    &:hover {
      box-shadow: 0 0 0 1px transparent, 0 0 0 1px $wl inset;
    }

    &:focus {
      box-shadow: 0 0 0 1px $p3, 0 0 0 1px $p3 inset;
    }

    &:disabled {
      background-color: $wh-ov24;
      color: $wh-ov24;
      &:hover {
        box-shadow: 0 0 0 1px transparent, 0 0 0 1px $wh-ov24 inset;
      }
    }
  }

  .error {
    color: $error;
    margin: 6px 0 0 0;
  }

  &.is-fullwidth {
    width: 100px;
    min-width: none;
  }
}
</style>