<template>
  <div id="logout-panel">
    <div class="user-info">
      <p class="user-name subtitle-3" :title="userName">{{ userName }}</p>
      <div class="user-avatar" :class="setAvatarStyle">
        <FetchImage :src="avatar" alt="User Avatar" />
      </div>
    </div>
    <ul class="logout-list">
      <li><router-link to="/pre-register" target="_blank" class="link-2 logout-listitem upgrade-to-plus">Upgrade to PLUS</router-link></li>
      <li><router-link to="/select-os" target="_blank" class="link-2 logout-listitem">Get BlackHole app</router-link></li>
      <li><button class="link-2 logout-listitem" @click="handleLogOut">Log Out</button></li>
    </ul>
  </div>
</template>

<script>
import FetchImage from "../components/FetchImage";
export default {
  name: "LogoutPanel",
  components: {
    FetchImage,
  },
  props: {
    userName: String,
    avatar: String,
  },

  methods: {
    handleLogOut() {
      this.$emit("logout");
    },
  },

  computed:{
    setAvatarStyle() {
      return this.avatar !== '/img/person_24px.png' ? 'avatar' : 'default';
    }
  }
};
</script>

<style lang="scss" scoped>
#logout-panel {
  position: absolute;
  top: -14px;
  right: -14px;
  width: 256px;
  border: 1px solid $wh-ov08;
  background-color: #1d1e1e;
  box-shadow: 0px 16px 32px rgba(0, 0, 0, 0.16),
    0px 8px 16px rgba(0, 0, 0, 0.24);
  cursor: default;

  .user-info {
    display: flex;
    align-items: center;
    justify-content: flex-end;

    padding: 12px 12px 12px 24px;
  }

  .user-avatar {
    position: relative;
    z-index: 2;
    border-radius: 50%;
    display: flex;
    width: 100%;
    max-width: 40px;
    height: 40px;
    border: 1px solid $wh-ov08;
    margin: 0 0 0 20px;
    cursor: pointer;
    box-sizing: border-box;

    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 40px;
      height: 40px;
      z-index: 2;
      border-radius: 50%;
    }

    &.default {
      transition: background-color 150ms ease-in-out;
      img {
        margin: auto;
        display: inline-block;
        width: 24px;
        height: 24px;
        opacity: 0.68;
      }
    }

    &.avatar {
      transition: background-color 150ms ease-in-out;
      img {
        margin: auto;
        display: inline-block;
        object-fit: cover;
        min-width: 38px;
        min-height: 38px;
        border-radius: 50%;
      }
    }
  }

  .user-name {
    color: $wm;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    max-width: 168px;
  }

  .logout-list {
    padding: 8px 0;
    border-top: 1px solid $wh-ov08;

    .logout-listitem {
      display: block;
      width: 100%;
      padding: 7px 24px 8px;
      text-align: left;
      color: $wh;
      transition: background-color 150ms ease-in-out;
      cursor: pointer;

      &:hover {
        background-color: $wh-ov02;
      }

      &:active {
        background-color: $wh-ov04;
      }

      &.upgrade-to-plus {
        color: $s1;
      }
    }
  }
}
</style>