<template>
  <div id="app-upload" ref="uploadApp">
    <div class="background-color">
      <div class="radial-gradient">
        <div class="dithering"></div>
      </div>
    </div>
    <div ref="contentLimiter" class="contents-limiter">
      <div class="app-block columns">
        <!-- Navbar -->
        <nav class="app-navbar column is-12 is-narrow-mobile">
          <router-link to="/web" class="logo">
            <img
              @click="clickOnLogo()"
              src="../assets/‌logo_24_wm.png"
              srcset="
                ../assets/‌logo_24_wm.png    1x,
                ../assets/‌logo_24_wm_2x.png 2x
              "
              alt="BlackHole Logo"
            />
          </router-link>

          <div class="navbar-right">
            <!-- App Launcher -->
            <div
              ref="appLauncher"
              class="app-launcher"
              @click.stop="handleAppLauncherClick"
            >
              <img
                src="../assets/apps_24px.png"
                alt="App Launcher icon"
                srcset="
                  ../assets/apps_24px.png    1x,
                  ../assets/apps_24px_2x.png 2x
                "
              />
              <AppLauncher v-show="isAppLauncherOpen"></AppLauncher>
            </div>

            <!-- Logout Panel -->
            <div
              ref="userAvatar"
              class="user-avatar"
              :class="avatar !== '/img/person_24px.png' ? 'avatar' : 'default'"
              @click.stop="handleLogoutPanelClick"
            >
              <FetchImage :src="avatar" alt="User Avatar" />
              <LogoutPanel
                v-show="isLoginPanelOpen"
                :userName="setUserName"
                :avatar="avatar"
                @logout="logOutFromBlockstack"
              >
              </LogoutPanel>
            </div>
          </div>
        </nav>
        <!-- Main content -->
        <main id="main" class="column is-12 is-narrow-mobile">
          <router-view @display-banner="doDisplay($event)"></router-view>
        </main>
        <!-- Footer -->
        <footer ref="footer" class="app-footer column is-12 is-narrow-mobile">
          <div class="columns is-vcentered">
            <div class="right column is-12">
              <p class="copyright overline">©2021 blackhole.</p>
              <router-link to="/legal" class="legal link-3">legal</router-link>
              <div ref="reportBlock" class="report-block">
                <button class="report-btn link-3" @click="onReportClick">
                  Report an issue<span class="arrow-icon"></span>
                </button>
                <ReportBlackHoleProblem
                  :isReportBlockOpen="isReportBlockOpen"
                  @submit="onSubmitBlackHoleProblemReport"
                  @cancel="onCancelDialog"
                  v-show="isReportBlockOpen"
                />
                <ReportAppreciation @done="onDoneButton" v-show="isThankDialogOpen" />
              </div>
            </div>
          </div>
        </footer>
        <!-- Banner -->
        <div ref="banner" class="banner is-hidden-mobile" v-show="isDisplayed">
          <div class="relative">
            <span class="close" @click="doClose()">
              <img
                src="../assets/i_close_op50_24px.png"
                alt="Close button"
                srcset="
                  ../assets/i_close_op50_24px.png    1x,
                  ../assets/i_close_op50_24px_2x.png 2x
                "
              />
            </span>
            <div class="os-icon">
              <img
                src="../assets/i_windows_24px.png"
                srcset="
                  ../assets/i_windows_24px.png    1x,
                  ../assets/i_windows_24px_2x.png 2x
                "
                alt="Windows icon"
                v-show="osName === 'Windows'"
              />
              <img
                src="../assets/i_mac_24px.png"
                srcset="
                  ../assets/i_mac_24px.png    1x,
                  ../assets/i_mac_24px_2x.png 2x
                "
                alt="Mac icon"
                v-show="osName === 'MacOS'"
              />
            </div>
            <h6 class="h6" style="text-align: center">
              Share files right from your desktop.
            </h6>
            <ul class="list">
              <li class="item">
                <img
                  src="../assets/i_done_op54_24px.png"
                  alt="Done icon"
                  srcset="
                    ../assets/i_done_op54_24px.png    1x,
                    ../assets/i_done_op54_24px_2x.png 2x
                  "
                />
                <span class="body-3">Upload hotkey</span>
              </li>
              <li class="item">
                <img
                  src="../assets/i_done_op54_24px.png"
                  alt="Done icon"
                  srcset="
                    ../assets/i_done_op54_24px.png    1x,
                    ../assets/i_done_op54_24px_2x.png 2x
                  "
                />
                <span class="body-3">Quicker upload</span>
              </li>
              <li class="item">
                <img
                  src="../assets/i_done_op54_24px.png"
                  alt="Done icon"
                  srcset="
                    ../assets/i_done_op54_24px.png    1x,
                    ../assets/i_done_op54_24px_2x.png 2x
                  "
                />
                <span class="body-3">Upload multiple folders</span>
              </li>
            </ul>
            <div class="download-link">
              <router-link
                to="/select-os"
                class="button-2"
                v-show="osName === 'Windows'"
                >Get the windows app</router-link
              >
              <router-link
                to="/select-os"
                class="button-2"
                v-show="osName === 'MacOS'"
                >Get the mac app</router-link
              >
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { userSession } from "../user";
import { setTimeout } from "timers";
import LogoutPanel from "../components/UploaderLogoutPanel";
import AppLauncher from "../components/UploaderAppLauncher";
import FetchImage from "../components/FetchImage";
import ReportBlackHoleProblem from "../components/ReportBlackHoleProblem";
import ReportAppreciation from '../components/ReportAppreciation';
import axios from "axios";

export default {
  name: "UploadApp",
  components: {
    LogoutPanel,
    AppLauncher,
    FetchImage,
    ReportBlackHoleProblem,
    ReportAppreciation
  },
  data() {
    return {
      logText: "log out",
      osName: "",
      // toggle banner display
      isDisplayed: false,
      isLoginPanelOpen: false,
      isAppLauncherOpen: false,
      avatar: "/img/person_24px.png",
      isReportBlockOpen: false,
      isThankDialogOpen: false,
    };
  },

  mounted() {
    if (!userSession.isUserSignedIn() && !userSession.isSignInPending())
      this.$router.push("/login");
    this.osDetection();
    this.setBannerTopPosition();
    this.setBannerRightPosition();
    let profile = userSession.loadUserData().profile;
    if (profile && profile.image) this.avatar = profile.image[0].contentUrl;
  },

  methods: {
    osDetection() {
      if (navigator.appVersion.indexOf("Win" || "Linux" || "Android") != -1) {
        this.osName = "Windows";
      }
      if (navigator.appVersion.indexOf("Mac" || "iPad" || "iPhone") != -1) {
        this.osName = "MacOS";
      }
    },

    logOutFromBlockstack() {
      userSession.signUserOut();
      this.$router.push("/landing");
    },
    doDisplay(time) {
      setTimeout(() => {
        this.setBannerTopPosition();
        this.isDisplayed = true;
      }, time);
    },
    clickOnLogo() {
      window.location.reload();
    },
    doClose() {
      this.isDisplayed = false;
      localStorage.removeItem("first_copy");
    },

    handleLogoutPanelClick() {
      this.isAppLauncherOpen = false;
      this.isLoginPanelOpen = true;
    },

    handleAppLauncherClick() {
      this.isAppLauncherOpen = true;
      this.isLoginPanelOpen = false;
    },

    handleDocumentClick(event) {
      if (this.isLoginPanelOpen) {
        const userAvatar = this.$refs.userAvatar;
        let target = event.target;
        if (!userAvatar || !userAvatar.contains(target)) {
          this.isLoginPanelOpen = false;
        }
      }

      if (this.isAppLauncherOpen) {
        const appLauncher = this.$refs.appLauncher;
        let target = event.target;
        if (!appLauncher || !appLauncher.contains(target)) {
          this.isAppLauncherOpen = false;
        }
      }

      if (this.isReportBlockOpen || this.isThankDialogOpen) {
        const reportBlock = this.$refs.reportBlock;
        let target = event.target;
        if (!reportBlock || !reportBlock.contains(target)) {
          this.isReportBlockOpen = false;
          this.isThankDialogOpen = false;
        }
      }
    },

    handleEscKeyDown(event) {
      if (event.keyCode === 27) {
        this.isLoginPanelOpen = false;
        this.isAppLauncherOpen = false;
        this.isReportBlockOpen = false;
        this.isThankDialogOpen = false;
      }
    },

    setBannerTopPosition() {
      let bannerTopHeight = this.$refs.banner.offsetTop + window.scrollY;
      let bannerHeight = this.$refs.banner.clientHeight;
      let footerTopHeight = this.$refs.footer.offsetTop;
      // let footerHeight = this.$refs.footer.clientHeight;
      // console.log(bannerTopHeight+bannerHeight- footerHeight,footerTopHeight)
      // console.log(window.scrollY)
      if (bannerTopHeight + bannerHeight > footerTopHeight + 32) {
        this.$refs.banner.style.transform = `translate(0, -${
          48 + (bannerTopHeight + bannerHeight) - (footerTopHeight + 32)
        }px)`;
      } else {
        this.$refs.banner.style.transform = "translate(0, -48px)";
      }
    },

    setBannerRightPosition() {
      this.$refs.banner.style.right = `${
        this.$refs.contentLimiter.offsetLeft - 16
      }px`;
    },

    onReportClick() {
      this.isReportBlockOpen = true;
    },

    onSubmitBlackHoleProblemReport(e) {
      axios.post("https://police.blackhole.run/api/report", {
        kind: "bug",
        url: window.location.href,
        key: "UploaderKey",
        message: e.issue,
        email: e.email
      });
      this.isReportBlockOpen = false
      this.isThankDialogOpen = true;
    },

    onCancelDialog() {
      this.isReportBlockOpen = false;
    },

    onDoneButton() {
      this.isThankDialogOpen = false;
    },
  },

  computed: {
    setUserName() {
      if (!userSession.isUserSignedIn()) return "";
      if (!userSession.loadUserData()) {
        window.location.reload();
      }
      let userId = userSession.loadUserData().username;
      if (userId) {
        let index = userId.indexOf(".id.blockstack");
        return userId.slice(0, index);
      }
      return "";
    },
  },

  created() {
    document.addEventListener("click", this.handleDocumentClick);
    document.addEventListener("keydown", this.handleEscKeyDown);
    window.addEventListener("scroll", this.setBannerTopPosition);
    window.addEventListener("resize", this.setBannerRightPosition);
  },
  destroyed() {
    // Important to clean up!!
    document.removeEventListener("click", this.handleDocumentClick);
    document.removeEventListener("keydown", this.handleEscKeyDown);
    window.removeEventListener("scroll", this.setBannerTopPosition);
    window.removeEventListener("resize", this.setBannerRightPosition);
  },
};
</script>

<style lang="scss" scoped>
@include override-gaps;
#app-upload {
  position: relative;
  min-height: 100vh;
  // min-height: 625px;
  overflow: hidden;

  @include until($tablet) {
    height: auto;
    min-height: 625px;
  }
}

.contents-limiter {
  height: 100%;
}

.background-color {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: $bh;
  z-index: -1;

  .radial-gradient {
    content: "";
    position: absolute;
    width: 2244px;
    height: 2244px;
    left: 172px;
    top: 230px;

    @include until($tablet) {
      left: -174px;
      top: -140px;
    }

    background: radial-gradient(
      1122px at 50% 50%,
      $p2 0%,
      $p3 37.5%,
      $p4 70.83%,
      $bh 100%
    );
    filter: opacity(0.25);
    .dithering {
      position: absolute;
      background-image: radial-gradient(
          1122px at 50% 50%,
          transparent 90%,
          $bh 100%
        ),
        url("../assets/noisy_texture_150px.png");
      width: 100%;
      height: 100%;
    }
  }
}

.app-block {
  display: flex;
  height: 100%;
  min-height: 100vh;
  justify-content: space-between;
  flex-direction: column;
  position: relative;
}

.app-navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 80px;

  @include until($tablet) {
    height: 64px;
    width: 100%;
    margin: 0;
    // to override bulma responsiveness in horizental mobile view
  }

  .logo {
    display: inline-block;
    height: 24px;
    margin: 0;
    outline: none;
    cursor: pointer;
    img {
      width: 132px;
      min-width: 132px;
      height: 24px;
    }
  }
}

.navbar-right {
  display: flex;
  align-items: center;
}

.app-footer {
  color: $wm;
  padding: 40px 0 40px;
  position: relative;

  @include until($tablet) {
    padding: 24px 24px 24px;
  }

  @include until(360px) {
    padding: 16px 24px 16px;
  }

  .right {
    display: flex;
    justify-content: flex-end;

    @include until($tablet) {
      justify-content: flex-start;
    }
  }
  .legal {
    margin: 0 0 0 12px;
    color: $wh;
    transition: color 150ms ease-in-out;
    &:hover {
      color: $wm;
    }

    @include until(360px) {
      margin: 0 0 0 8px;
    }
  }

  .report-block {
    height: 12px;
    margin: 0 0 0 12px;
    z-index: 20;

    &::v-deep .base-dialog {
      position: absolute;
      bottom: 24px;
      right: -16px;

      @include until($tablet) {
        bottom: 24px;
        right: 32px;
      }

      @include until(424px) {
        right: 50%;
        bottom: 24px;
        transform: translate(50%, 0);
      }

      @include until(408px) {
        right: 50%;
        bottom: 0;
        transform: translate(50%, 0);
      }

      @include until(360px) {
        bottom: 0;
        right: 0;
        left: 0;
        transform: translate(0, 0);
      }
    }

    @include until(360px) {
      margin: 0 0 0 8px;
    }

    .report-btn {
      display: block;
      box-sizing: border-box;
      margin: -1px 0 0 0;
      padding: 0;
      background-color: transparent;
      border: none;
      outline: none;
      cursor: pointer;
      color: $wh;

      .arrow-icon {
        position: relative;
        display: inline-block;
        width: 10px;
        height: 10px;
        margin: 0 0 1px 2px;
        &::after {
          content: "";
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%) rotate(45deg);
          // background: $wh;
          border-bottom: 3px solid $wh;
          border-right: 3px solid $wh;
          border-top: 3px solid transparent;
          border-left: 3px solid transparent;
        }
      }
    }
  }
}

.logout {
  color: $wm;
  padding: 7px 0 0 0;
  cursor: pointer;
}

.banner {
  position: fixed;
  bottom: 0;
  width: 223px;
  height: 363px;
  background: rgba(20, 104, 123, 0.16);

  .relative {
    width: 100%;
    height: 100%;
    position: relative;
    padding: 20px;
    .close {
      position: absolute;
      top: 12px;
      right: 12px;
      cursor: pointer;
      z-index: 1;
    }
  }

  .os-icon {
    display: flex;
    justify-content: space-around;
    padding: 32px 0 24px;
    img {
      width: 52px;
      height: 52px;
    }
  }

  .h6 {
    color: $wh;
    margin: 0 0 17px;
  }

  .list {
    color: $wm;
    margin: 0 0 41px;
    .item {
      display: flex;
      align-items: center;
      margin: 0 0 15px;

      img {
        margin: -1px 8px 0 0;
      }
    }
  }

  .download-link {
    .button-2 {
      color: $p1;
      display: block;
      margin: 0 auto 8px;
      text-align: center;
    }
  }
}

#main {
  margin: 100px 0 88px;
}

.user-avatar {
  position: relative;
  z-index: 2;
  border-radius: 50%;
  display: flex;
  width: 40px;
  height: 40px;
  border: 1px solid $wh-ov08;
  margin: 0 0 0 8px;
  cursor: pointer;
  box-sizing: border-box;

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 40px;
    height: 40px;
    z-index: 2;
    // background-color: black;
    border-radius: 50%;
  }

  &.default {
    transition: background-color 150ms ease-in-out;
    &:hover {
      &::after {
        background-color: $wh-ov02;
      }
    }
    &:active {
      &::after {
        background-color: $wh-ov04;
      }
    }
    img {
      margin: auto;
      display: inline-block;
      width: 24px;
      height: 24px;
      opacity: 0.68;
    }
  }

  &.avatar {
    transition: background-color 150ms ease-in-out;
    &:hover {
      &::after {
        background-color: $bl-ov04;
      }
    }
    &:active {
      &::after {
        background-color: $bl-ov08;
      }
    }
    img {
      margin: auto;
      display: inline-block;
      object-fit: cover;
      min-width: 38px;
      min-height: 38px;
      border-radius: 50%;
    }
  }
}

.app-launcher {
  position: relative;
  display: flex;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  transition: background-color 150ms ease-in-out;
  cursor: pointer;
  &:hover {
    background-color: $wh-ov02;
  }
  &:active {
    background-color: $wh-ov04;
  }

  img {
    display: inline-block;
    margin: auto;
    width: 24px;
    height: 24px;
  }
}
</style>
