            <h4 class="h4">Privacy Policy</h4>
            <p class="body-2">
              We care about privacy and security. This page describe how we
              handle your information in an easy and transparent way. You can
              also check the open source code here.
            </p>
            <p class="subtitle-1">Files</p>
            <p class="body-2">
              All files you share will be encrypted using advanced security
              standards and then sent to a random hub to be stored. Encryption
              key will be stored in your device and private hub as encrypted
              with your identity key.
            </p>
            <p class="subtitle-1">Desktop App</p>
            <p class="body-2">
              We collect interaction and technical information such as how users
              interact with features, operating system, error reasons, language.
            </p>
            <p class="subtitle-1">Web App</p>
            <p class="body-2">
              We do not collect information about your device and what you get.
              We do not track you across the app nor store any cookie.
            </p>
