<script>
export default {
  name: "legal",

  methods: {
    navigator(event) {
      if (event.target.innerHTML === "Privacy Policy") {
        this.$ga.event({
          eventCategory: "navigator",
          eventAction: "navigate_to_privacy_policy",
          eventLabel: "navigator",
          eventValue: 0
        });
      } else if (event.target.innerHTML === "Terms of Service") {
        this.$ga.event({
          eventCategory: "navigator",
          eventAction: "navigate_to_terms_of_service",
          eventLabel: "navigator",
          eventValue: 0
        });
      } else {
        this.$ga.event({
          eventCategory: "navigator",
          eventAction: "navigate_to_third_party",
          eventLabel: "navigator",
          eventValue: 0
        });
      }
    }
  }
};
</script>

