<template>
  <footer :style="{display: visibleFooter ? 'block' : 'none'}">
    <div class="contents-limiter">
      <div class="columns">
        <div class="column footer-block">
          <div class="columns is-full">
            <div class="column left is-3">
              <!-- BlackHole logo -->
              <div class="logo-block">
                <router-link to="/" @click.native="footerHomeClick">
                  <img
                    src="../assets/‌logo_24_wm.png"
                    srcset="../assets/‌logo_24_wm.png 1x, ../assets/‌logo_24_wm_2x.png 2x"
                    class="footer-logo"
                    alt="BlackHole Logo"
                  />
                </router-link>
              </div>
            </div>

            <div class="column right is-9">
              <div class="copyright">
                <span class="overline">©2021 BLACKHOLE Co. ALL RIGHTS RESERVED.</span>
                <router-link to="/legal" @click.native="footerLegalClick" class="legal link-3">LEGAL</router-link>
              </div>
              <div class="social">
                <!-- Download page -->
                <router-link
                  to="/select-os"
                  @click.native="footerDownloadClick"
                  class="link-2"
                >Download</router-link>
                <!-- Contact us -->
                <a href="mailto:hi@blackhole.run" class="link-2" targer="_blank">Contact</a>
                <!-- Newsletter page -->
                <router-link
                  to="/newsletter"
                  @click.native="footerNewsletterClick"
                  class="link-2"
                >Newsletter</router-link>
                <!-- GitHub link -->
                <div class="imglink">
                  <a
                    href="https://github.com/blackholeorganization"
                    @click="footerGithubClick"
                    target="_blank"
                  >
                    <img
                      src="../assets/i_github.png"
                      alt="Github icon"
                      srcset="../assets/i_github.png 1x, ../assets/i_github_2x.png 2x"
                    />
                  </a>
                  <!-- Producthunt link -->
                  <a href="https://www.producthunt.com/posts/blackhole" target="_blank">
                    <img
                      src="../assets/i_producthunt.png"
                      alt="ProductHunt icon"
                      srcset="../assets/i_producthunt.png 1x, ../assets/i_producthunt_2x.png 2x"
                    />
                  </a>
                  <!-- Twitter link -->
                  <a
                    href="https://twitter.com/BlackHoleBH_"
                    @click="footerTwitterClick"
                    target="_blank"
                  >
                    <img
                      src="../assets/i_twitter.png"
                      alt="Twitter icon"
                      srcset="../assets/i_twitter.png 1x, ../assets/i_twitter_2x.png 2x"
                    />
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </footer>
</template>
<script>
export default {
  props:{
    visibleFooter: Boolean,
  },

  methods: {
    footerHomeClick() {
      this.$ga.event({
        eventCategory: "Router",
        eventAction: "from_footer_to_home",
        eventLabel: "footerHomeClick",
        eventValue: 0
      });
    },

    footerLegalClick() {
      this.$ga.event({
        eventCategory: "Router",
        eventAction: "from_footer_to_legal",
        eventLabel: "footerLegalClick",
        eventValue: 0
      });
    },

    footerNewsletterClick() {
      this.$ga.event({
        eventCategory: "Router",
        eventAction: "from_footer_to_newsletter",
        eventLabel: "footerNewsletterClick",
        eventValue: 0
      });
    },

    footerGithubClick() {
      this.$ga.event({
        eventCategory: "Hyperlink",
        eventAction: "from_footer_to_bh_github",
        eventLabel: "footerGithubClick",
        eventValue: 0
      });
    },

    footerTwitterClick() {
      this.$ga.event({
        eventCategory: "Hyperlink",
        eventAction: "from_footer_to_bh_twitter",
        eventLabel: "footerTwitterClick",
        eventValue: 0
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@include override-gaps;

.footer-block {
  border-top: 1px solid $bl;
  padding: 35px 0 40px;
  margin: 0 1rem;
  @include until($tablet) {
    margin: 0 1.5rem;
    flex-direction: column;
  }
}

.right {
  display: flex;
  justify-content: space-between;
  padding: 0 16px 0 32px;
  @include until($tablet) {
    padding: 0 24px 0;
    flex-direction: column;
  }
}

.logo-block {
  display: flex;
  align-self: center;
  @include until($tablet) {
    order: 1;
    margin: 0 0 40px;
  }
  .footer-logo {
    width: 132px;
    height: 24px;
  }
}

.copyright {
  color: $wm;
  display: flex;
  align-self: center;
  flex-wrap: wrap;
  @include until($tablet) {
    order: 3;
    align-self: flex-start;
  }

  .overline {
    margin: 0 7px 0 0;
  }

  .legal {
    color: $wh;
  }
}

.social {
  display: flex;
  align-self: center;
  @include until($tablet) {
    order: 2;
    align-self: flex-start;
    flex-direction: column;
    margin: 0 0 32px;
  }

  a {
    display: inline-block;
    margin: auto 24px auto 0;
    color: $wh;
    @include until($tablet) {
      margin: 0 0 12px;
    }
  }

  a:last-child {
    margin: 0 3px 0 0;
    color: $wh;
    @include until($tablet) {
      margin: 0;
    }
  }
}

.imglink {
  display: flex;
  align-items: center;
  margin: 0 0 0 10px;
  a {
    display: inline-block;
    width: 24px;
    height: 24px;
    transition: opacity 150ms ease-in-out;
    &:hover {
      opacity: 0.6;
    }
    img {
      display: block;
      width: 24px;
      height: 24px;
    }
  }

  @include until($tablet) {
    margin: 0;
    align-items: flex-start;
    a {
      margin: 0 21px 0 0;
      &:last-child {
        margin: 0;
      }
    }
  }
}

.link-2,
.link-3 {
  transition: color 150ms ease-in-out;
  &:hover {
    color: $wm;
  }
}
</style>
