<script>
import { userSession } from "../user";
export default {
  data() {
    return {
      isVisible: true
    };
  },

  mounted() {
    this.isVisible = !userSession.isUserSignedIn();
  },

  methods: {
    getBlackHoleButton() {
      this.$ga.event({
        eventCategory: "Router",
        eventAction: "from_home_pricing_to_select_os",
        eventLabel: "GetBlackHoleButton",
        eventValue: 0
      });
    },

    continueWithPlusButton() {
      this.$ga.event({
        eventCategory: "Router",
        eventAction: "from_home_pricing_to_plus_pre_register",
        eventLabel: "continueWithPlusButton",
        eventValue: 0
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@include override-gaps;

.pricing {
  padding: 160px 0;
  @include until($tablet) {
    padding: 96px 0;
  }
  .column {
    @include until($tablet) {
      padding: 0;
    }
  }
}

.h4 {
