<template>
  <section id="faq" class="columns">
    <div class="column is-9 is-offset-3">
      <h5 class="h5 faq-title">Frequently asked questions</h5>
      <ul class="faq-list">
        <li v-for="(faq,index) in faqs" :key="index">
          <div @click="expandItem(faq, index)" class="faq-item">
            <!-- Question -->
            <p class="question subtitle-1" :class="[{'text-color': faq.isOpen}]">{{ faq.question }}</p>
            <!-- Answer -->
            <div
              class="answer"
              :style="[faq.isOpen ? {maxHeight: faq.answerMaxHeight + 'px'} : {maxHeight: 0}]"
            >
              <p ref="answerText" class="body-2">
                <span v-html="faq.answer"></span>
              </p>
            </div>
            <!-- Arrow icon -->
            <div class="arrow-block">
              <div class="arrow">
                <span :class="[{'animate-1': faq.isOpen}]" class="hand-1"></span>
                <span :class="[{'animate-2': faq.isOpen}]" class="hand-2"></span>
              </div>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      isOpen: false,
      faqs: [
        {
          question: "What is BlackHole? How much does it cost?",
          answer:
            "BlackHole is a file transfer application built on top of blockchain for \
            the new internet. You can share any super security file with ease and be \
            sure the data is yours forever.<br/>You can use BlackHole for free, with no \
            storage or bandwidth limit, but for files bigger than 512 MB, there is an \
            upcoming PLUS plan. You can signup for PLUS plan news \
            <a href='https://blackhole.run/pre-register' target='_blank' rel='noopener noreferrer' class='faq-link'>here.</a>",
          isOpen: false,
          answerMaxHeight: 0
        },
        {
          question: "Where are my uploaded files?",
          answer:
            "BlackHole encrypts the files and then send them to a hub of your choice. \
            Thanks to Blockstack, BlackHole comes with free unlimited storage. You also \
            have an option to chose your own storage provider if you want. There is no \
            difference in what provider you are using; BlackHole will encrypt all your \
            data with the highest security possible.",
          isOpen: false,
          answerMaxHeight: 0
        },
        {
          question: "How do you protect my personal information?",
          answer:
            "BlackHole operates as a transparent and secure system based on Bitcoin \
            blockchain. It will always encrypt everything using ECIES and AES256 \
            encryption, and only a specific recipient key can decrypt it. Also, the \
            protocol codebase is open-source and hosted on GitHub. You can check it out \
            <a href='https://github.com/blackholeorganization' target='_blank' rel='noopener noreferrer' class='faq-link'>here</a>.",
          isOpen: false,
          answerMaxHeight: 0
        },
        {
          question: "Why does decentralization matter?",
          answer:
            "Centralization often means providers are having access to your data without \
            you knowing or approving. Decentralization makes it harder or even impossible; \
            it also allows you to have control over your data. Read more about it \
            <a href='https://docs.blockstack.org/faqs/allfaqs#what_is_a_decentralized_internet' \
            target='_blank' rel='noopener noreferrer' class='faq-link'>here</a>.",
          isOpen: false,
          answerMaxHeight: 0
        },
        {
          question: "How is BlackHole for everyday use?",
          answer:
            "No need to organize files before sending, select any number of files or \
            folders then upload them right from where they are with Command+B (or Ctrl+B \
            if you are on Windows). Alternatively, you can drag-and-drop a file on the \
            BlackHole tray icon.",
          isOpen: false,
          answerMaxHeight: 0
        },
        {
          question: "How can I transfer a file with password protection?",
          answer:
            "Press Command+Shift+B (or Ctrl+Shift+B if you are on Windows) on any \
            selected file, then BlackHole will pop up and be waiting for you to set \
            the transfer password. Alternatively, you can hold the Shift key and \
            drag-and-drop a file on the BlackHole tray icon.",
          isOpen: false,
          answerMaxHeight: 0
        },
        {
          question: "Didn't find an answer to your question?",
          answer:
            "You can always contact us from \
            <a href='mailto:hi@blackhole.run' target='_blank' rel='noopener noreferrer' class='faq-link'>here</a>,\
            and we'll get back to you ASAP.",
          isOpen: false,
          answerMaxHeight: 0
        }
      ]
    };
  },

  methods: {
    expandItem(faq, index) {
      const maxHeight = this.$refs.answerText[index].clientHeight + 10;
      this.$set(this.faqs, index, {
        question: faq.question,
        answer: faq.answer,
        isOpen: !faq.isOpen,
        answerMaxHeight: maxHeight
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@include override-gaps;
#faq {
  padding: 160px 0;
}

.faq-list {
  width: 83%;
  margin: 0 17% 0 16px;
  @include until($tablet) {
    width: 100%;
    margin: 0;
  }
}

li {
  border-top: 1px solid $bl;
  &:last-child {
    border-bottom: 1px solid $bl;
  }

  .faq-item {
    cursor: pointer;
    padding: 22px 96px 22px 0;
    position: relative;

    @include until($tablet) {
      padding: 22px 72px 22px 0;
    }

    // Change color of question and arrow icon on hover
    &:hover {
      .question {
        color: $wh;
      }

      .hand-1 {
        background-color: $wh;
      }
      .hand-2 {
        background-color: $wh;
      }
    }

    .question {
      color: $wm;
    }

    .text-color {
      color: $wh;
    }

    .answer {
      color: $wm;
      height: auto;
      max-height: 0;
      overflow: hidden;
      transition: max-height 450ms cubic-bezier(0.23, 1, 0.32, 1);
      .body-2 {
        margin: 10px 0 0 0;
      }
    }
  }
}

.arrow-block {
  width: 24px;
  height: 24px;
  position: absolute;
  top: 24px;
  right: 24px;
  display: flex;
  align-items: center;
  justify-content: center;

  .arrow {
    width: 10px;
    height: 10px;
    position: relative;
    transform: rotate(225deg);

    .hand-1 {
      position: absolute;
      width: 10px;
      height: 1.5px;
      background-color: $wm;
      transform-origin: center;
      transition: transform 200ms cubic-bezier(0.23, 1, 0.32, 1);
    }

    .hand-2 {
      position: absolute;
      width: 1.5px;
      height: 10px;
      background-color: $wm;
      transform-origin: center;
      transition: transform 200ms cubic-bezier(0.23, 1, 0.32, 1);
    }

    .animate-1 {
      transform: rotate(-90deg);
      background-color: $wh;
    }

    .animate-2 {
      transform: rotate(90deg);
      background-color: $wh;
    }
  }
}

///deep/.faq-link {
//  color: $wm;
//  transition: color 150ms cubic-bezier(0.23, 1, 0.32, 1);
//  border-bottom: 1px solid $wm;
//  display: inline-block;
//  &:hover {
//    color: $wh;
//    border-bottom: 1px solid $wh;
//  }
//}

.faq-title {
  color: $wh;
  margin: 0 0 71px 16px;
}
</style>
