<template>
  <div class="report-blackhole-problem">
    <base-dialog position="bottom-right">
      <h2 class="subtitle-2">Report a problem with BlackHole</h2>
      <BaseInput
        type="email"
        name="user-email"
        placeholder="Your email address (optional)"
        :value="email"
        @input="onInputEmail"
      />
      <BaseTextarea
        name="issue-describtion"
        placeholder="Tell us more about the issue"
        :error="error"
        :value="issue"
        @input="onInputIssueDescription"
        @focus="onFocusIssueDescription"
        @blur="onBlurIssueDescription"
      />
      <base-button variant="secondary" @click="onCancel">Cancel</base-button>
      <base-button variant="primary" @click="onSubmit" is-fullwidth>Submit</base-button>
    </base-dialog>
  </div>
</template>

<script>
import BaseTextarea from "./BaseTextarea.vue";
import BaseDialog from "./BaseDialog.vue";
import BaseButton from "./BaseButton.vue";
import BaseInput from "./BaseInput.vue";

export default {
  name: "ReportBlackHoleProblem",
  components: {
    BaseTextarea,
    BaseInput,
    "base-dialog": BaseDialog,
    "base-button": BaseButton,
  },

  props: {
    isReportBlockOpen: Boolean,
  },

  data() {
    return {
      email: "",
      issue: "",
      error: "",
    };
  },

  methods: {
    onCancel() {
      this.$emit("cancel");
    },
    onSubmit() {
       if (this.issue === "") {
        this.error = "Issue description is required";
      } else {
        this.error = "";
        this.$emit("submit", { email: this.email, issue: this.issue });
      }
    },
    onInputEmail(e) {
      this.email = e.value;
    },

    onInputIssueDescription(e) {
      this.issue = e.value;
    },

    onFocusIssueDescription() {
      this.error = "";
    },

    onBlurIssueDescription() {
      if (this.issue === "") {
        this.error = "Issue description is required";
      } else {
        this.error = "";
      }
    },
  },

  watch: {
    isReportBlockOpen: function () {
      if (this.isReportBlockOpen === false) {
        this.error = "";
        this.issue =  "";
        this.email =  "";
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.report-blackhole-problem {
  &::v-deep .base-dialog {
    grid-template-columns: min-content 1fr;
    grid-column-gap: 8px;
    grid-template-areas:
      "title title"
      "input input"
      "textarea textarea"
      "cancel submit";
    .subtitle-2 {
      grid-area: title;
      color: $wh;
      margin: 0 0 24px;
    }

    .base-input {
      grid-area: input;
      margin: 0 0 16px;
      .input {
        margin: 0;
      }
    }

    .base-textarea {
      grid-area: textarea;
      margin: 0 0 24px;
      .textarea {
        height: 236px;
      }
    }

    .base-button {
      &:first-child {
        grid-area: cancel;
      }
      &:nth-child(2) {
        grid-area: submit;
      }
    }
  }
}
</style>