<template>
  <div id="select-os">
    <div class="contents-limiter">
      <div class="columns">
        <div class="column select-block is-offset-3">
          <h4 class="h4">BlackHole for Desktop and Mobile</h4>
          <p class="subtitle-1">Share files directly within your app.</p>

          <div class="button-group">
            <div class="order-block" :style="setOrder('Mac OS')">
              <base-button to="/download#mac" :variant="setVariant('Mac OS')" @click="selectMacVersion" has-img is-fullwidth>
                <inline-svg :src="require('../assets/mac_24px.svg')"/>
                <span>Download for mac</span>
              </base-button>
            </div>

            <div class="order-block" :style="setOrder('Windows')">
              <base-button to="/download#win" :variant="setVariant('Windows')" @click="selectWinVersion" has-img is-fullwidth>
                <inline-svg :src="require('../assets/windows_24px.svg')"/>
                <span>Download for windows</span>
              </base-button>
            </div>

            <div class="order-block" :style="setOrder('Android')">
              <base-button to="/download#android" :variant="setVariant('Android')" has-img is-fullwidth>
                <inline-svg :src="require('../assets/android_24px.svg')"/>
                <span>Download for android</span>
              </base-button>
            </div>
          </div>

          <p class="body-3">Available for Windows 10, macOS 10.14+, and Android 8+</p>
          <p class="body-3">All the apps are signed but not with code signing certification from operating system providers. As a decentralized service, BlackHole does not abide by the rules of central companies. Your operation system may make it hard to allow installing uncertified software on your device.</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BaseButton from "../components/BaseButton.vue";
import InlineSvg from "vue-inline-svg";
export default {
  name: "select-os",
  components: {
    "base-button": BaseButton,
    "inline-svg": InlineSvg,
  },
  data() {
    return {
      os: "",
    }
  },
  methods: {
    selectWinVersion() {
      this.$ga.event({
        eventCategory: "Router",
        eventAction: "download_blackhole_win_version",
        eventLabel: "selectWinVersion",
        eventValue: 0,
      });
    },

    selectMacVersion() {
      this.$ga.event({
        eventCategory: "Router",
        eventAction: "download_blackhole_mac_version",
        eventLabel: "selectMacVersion",
        eventValue: 0,
      });
    },

    setOrder(os) {
      return {order: this.os === os ? 0 : 1}
    },

    setVariant(os) {
      return this.os === os ? 'primary' : 'secondary';
    },
  },

  mounted() {
      let userAgent = window.navigator.userAgent,
        platform = window.navigator.platform,
        macosPlatforms = ["Macintosh", "MacIntel", "MacPPC", "Mac68K"],
        windowsPlatforms = ["Win32", "Win64", "Windows", "WinCE"],
        iosPlatforms = ["iPhone", "iPad", "iPod"],
        os = "";

      if (macosPlatforms.indexOf(platform) !== -1) {
        os = "Mac OS";
      } else if (iosPlatforms.indexOf(platform) !== -1) {
        os = "iOS";
      } else if (windowsPlatforms.indexOf(platform) !== -1) {
        os = "Windows";
      } else if (/Android/.test(userAgent)) {
        os = "Android";
      } else if (!os && /Linux/.test(platform)) {
        os = "Linux";
      }

      this.os = os;
  }
};
</script>

<style lang="scss" scoped>
@include override-gaps;
#select-os {
  min-height: 100vh;
  // min-height: 721px;
}

.select-block {
  padding: 288px 0 168px 2rem;
  margin: 0 21% 0 25%;
  @include until($tablet) {
    padding: 232px 1.5rem 214px;
    margin: 0;
  }
}

.h4 {
  color: $wh;
  margin: 0 0 5px;
}

.subtitle-1 {
  margin: 0 0 112px;
  color: $wm;

  @include until($tablet) {
    margin: 0 0 125px;
  }
}

.button-group {
  display: flex;
  flex-wrap: wrap;
  width: 272px;
  margin: 0 0 16px;

  &::v-deep .order-block {
      flex: 1;
      order: 1;
      margin: 0 0 10px;
  }
}

.body-3 {
  color: $wm;
  margin: 0 0 12px;
  &:last-child {
    margin: 0;
    max-width: 509px;
  }
}
</style>
